import login from '@/views/login';
import index from '@/views/index';
import home from '@/views/home';
import NotFound from '@/views/404';
// 用户
import UserInfo from '@/views/user/UserInfo';
// 礼品卡
import CardConfig from '@/views/card/CardConfig';
import CardGive from '@/views/card/CardGive';
// 活动
import FreeSheet from '@/views/activity/FreeSheet';
import ReceiveList from '@/views/activity/ReceiveList';
// 门店
import Clerk from '@/views/store/Clerk';
import Commission from '@/views/store/Commission';
import Achievement from '@/views/store/Achievement';
// 财务-信息
import Account from '@/views/finance/cardInfo/Account';
import Purchase from '@/views/finance/cardInfo/Purchase';
import Consume from '@/views/finance/cardInfo/Consume';
import Settlement from '@/views/finance/cardInfo/Settlement';
import Retreat from '@/views/finance/cardInfo/Retreat';
// 财务-月结
import MonthlySettle from '@/views/finance/MonthlySettle';
// 财务-储值卡
import Abnormal from '@/views/finance/deposit/Abnormal';
import Consumption from '@/views/finance/deposit/Consumption';
import Recharge from '@/views/finance/deposit/Recharge';
import InforImport from '@/views/finance/deposit/InforImport';

// 系统设置
import UserSystem from '@/views/system/UserSystem';
import roleSystem from '@/views/system/roleSystem';
import Brand from '@/views/system/BrandInfo/Brand';
import Area from '@/views/system/BrandInfo/Area';
import Store from '@/views/system/BrandInfo/Store';
import Terminal from '@/views/system/BrandInfo/Terminal';




import Excel from '@/views/user/excel'


let routes = [{
    path: '/login',
    name: 'login',
    component: login,
    hidden: true
  }, {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  //  {
  //   path: '/home',
  //   component: index,
  //   // redirect: { name: '用户信息' },
  //   name: 'home',
  //   iconCls: 'icon-tab_index',
  //   meta: {
  //     title: '首页'
  //   },
  // },
  {
    path: '/user',
    component: index,
    name: 'user',
    iconCls: 'icon-tab_user',
    meta: {
      title: '用户管理'
    },
    children: [{
      path: '/user/UserInfo',
      component: UserInfo,
      name: 'UserInfo',
      meta: {
        title: '用户信息'
      }
    }, {
      path: '/user/Excel',
      component: Excel,
      name: 'Excel',
      meta: {
        title: 'Excel'
      }
    },]
  }, {
    path: '/card',
    component: index,
    name: 'card',
    iconCls: 'icon-tab_Giftcard',
    meta: {
      title: '礼品卡管理'
    },
    children: [{
      path: '/card/CardConfig',
      component: CardConfig,
      name: 'CardConfig',
      meta: {
        title: '礼品卡配置'
      }
    }, {
      path: '/card/CardGive',
      component: CardGive,
      name: 'CardGive',
      meta: {
        title: '礼品卡转赠'
      }
    }, ]
  }, {
    path: '/activity',
    component: index,
    hidden: true,
    name: 'activity',
    iconCls: 'icon-tab_activity',
    meta: {
      title: '营销活动'
    },
    children: [{
      path: '/activity/FreeSheet',
      component: FreeSheet,
      name: 'FreeSheet',
      meta: {
        title: '免单活动'
      },
    }, {
      path: '/activity/ReceiveList',
      component: ReceiveList,
      name: 'ReceiveList',
      meta: {
        title: '领取明细'
      },
    }]
  }, {
    path: '/store',
    component: index,
    name: 'store',
    iconCls: 'icon-tab_shop',
    meta: {
      title: '门店管理'
    },
    children: [{
      path: '/store/Clerk',
      component: Clerk,
      name: 'Clerk',
      meta: {
        title: '门店店员管理'
      },
    }, {
      path: '/store/Commission',
      component: Commission,
      name: 'Commission',
      meta: {
        title: '售卡分佣规则'
      },
    }, {
      path: '/store/Achievement',
      component: Achievement,
      name: 'Achievement',
      meta: {
        title: '售卡业绩统计'
      },
    }]
  }, {
    path: '/finance',
    component: index,
    name: 'finance',
    iconCls: 'icon-tab_Finance',
    meta: {
      title: '财务管理'
    },
    children: [{
        path: '/finance/MonthlySettle',
        component: MonthlySettle,
        name: 'MonthlySettle',
        meta: {
          title: '分佣业绩月结'
        },
      }, {
        path: '/finance/cardInfo',
        name: 'cardInfo',
        meta: {
          title: '礼品卡信息管理'
        },
        component: {
          render(c) {
            return c('router-view')
          }
        },
        children: [{
          path: '/finance/cardInfo/Account',
          component: Account,
          name: 'Account',
          meta: {
            title: '礼品卡账户明细'
          },
        }, {
          path: '/finance/cardInfo/Purchase',
          component: Purchase,
          name: 'Purchase',
          meta: {
            title: '礼品卡购卡明细'
          },
        }, {
          path: '/finance/cardInfo/Consume',
          component: Consume,
          name: 'Consume',
          meta: {
            title: '礼品卡消费明细'
          },
        }, {
          path: '/finance/cardInfo/Settlement',
          component: Settlement,
          name: 'Settlement',
          meta: {
            title: '礼品卡消费结算'
          },
        }, {
          path: '/finance/cardInfo/Retreat',
          component: Retreat,
          name: 'Retreat',
          meta: {
            title: '礼品卡退卡退款'
          },
        }]
      },
      {
        path: '/finance/deposit',
        name: 'deposit',
        meta: {
          title: '清结算储值管理'
        },
        component: {
          render(c) {
            return c('router-view')
          }
        },
        children: [{
          path: '/finance/deposit/InforImport',
          component: InforImport,
          name: 'InforImport',
          meta: {
            title: '储值卡信息导入'
          },
        }, {
          path: '/finance/deposit/Recharge',
          component: Recharge,
          name: 'Recharge',
          meta: {
            title: '储值卡充值明细'
          },
        }, {
          path: '/finance/deposit/Consumption',
          component: Consumption,
          name: 'Consumption',
          meta: {
            title: '储值卡消费明细'
          },
        }, {
          path: '/finance/deposit/Abnormal',
          component: Abnormal,
          name: 'Abnormal',
          meta: {
            title: '储值卡异常处理'
          },
        }, ]
      }
    ]
  }, {
    path: '/system',
    component: index,
    name: 'system',
    iconCls: 'icon-tab_Systemsetting',
    meta: {
      title: '系统设置'
    },
    children: [{
      path: '/system/UserSystem',
      component: UserSystem,
      name: 'UserSystem',
      meta: {
        title: '系统用户'
      },
    }, {
      path: '/system/roleSystem ',
      component: roleSystem,
      name: 'roleSystem',
      meta: {
        title: '角色权限'
      },
    }, {
      path: '/system/BrandInfo',
      name: 'BrandInfo',
      meta: {
        title: '品牌信息'
      },
      component: {
        render(c) {
          return c('router-view')
        }
      },
      children: [{
        path: '/system/BrandInfo/Brand',
        component: Brand,
        name: 'Brand',
        meta: {
          title: '品牌数据'
        },
      }, {
        path: '/system/BrandInfo/Area',
        component: Area,
        name: 'Area',
        meta: {
          title: '区域数据'
        },
      }, {
        path: '/system/BrandInfo/Store',
        component: Store,
        name: 'Store',
        meta: {
          title: '门店数据'
        },
      }, {
        path: '/system/BrandInfo/Terminal',
        component: Terminal,
        name: 'Terminal',
        meta: {
          title: '终端管理'
        },
      }]
    }]
  }
];

export default routes;



