<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>{{tempName}}</div>
    </div>
    <div class="mainTable_search">
      <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
    </div>
    <div class="mainTable_content">
      <table-base
        :tableBut="tableBut"
        :tableSelection="tableSelection"
        :tableExpand="tableExpand"
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableOperate="tableOperate"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        @butEvent="butEvent"
        @operateEvent="operateEvent"
        @selectionEvent="selectionEvent"
        @filterEvent="filterEvent"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
        ref="tableBase"
      ></table-base>
    </div>
    <div class="mainTable_dialog">
      <el-dialog
        width="580px"
        title="店员信息"
        top="10vh"
        v-if="dialogFormVisible"
        :visible.sync="dialogFormVisible"
        :close-on-click-modal="false"
      >
        <clerk-edit :formData="formData" @cancelForm="cancelForm" @click="submitForm"></clerk-edit>
      </el-dialog>

      <el-dialog
        title="导入文件"
        :visible.sync="dialogImportVisible"
        v-if="dialogImportVisible"
        width="380px"
      >
        <import-box @downTempExcel="downTempExcel" @importTempExcel="importTempExcel"></import-box>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import clerkEdit from "./clerkEdit.vue";
import XLSX from "xlsx";
export default {
  name: "Clerk",
  data() {
    return {
      tempName: "门店店员管理",

      searchData: {},

      formData: {},
      dialogFormVisible: false,
      dialogImportVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      searchList: [
        {
          id: "input",
          type: "input",
          name: "input",
          title: "店员姓名",
          placeholder: "请输入店员姓名"
        },
        {
          id: "input",
          type: "input",
          name: "input",
          title: "店员工号",
          placeholder: "请输入店员工号"
        },
        {
          id: "cascader",
          type: "cascader",
          name: "cascader",
          title: "品牌门店",
          placeholder: "请选择品牌门店"
        },
        {
          id: "select",
          type: "select",
          name: "select",
          title: "店员状态",
          placeholder: "请选择店员状态",
          options: [
            {
              name: "lll",
              value: "ooo"
            }
          ]
        }
      ],

      tableBut: [
        { name: "新 增", value: "edit", show: true },
        { name: "导 出", value: "export", show: true, title: "门店店员管理" },
        { name: "导 入", value: "import", show: true },
        { name: "生成店员二维码", value: "expfort", show: true }
      ],
      tableHead: [
        { name: "店员姓名", value: "name" },
        { name: "店员工号", value: "date1" },
        { name: "手机号码", value: "date" },
        { name: "店员角色", value: "a" },
        { name: "所属品牌", value: "prosgress" },
        { name: "所属区域", value: "proasdgress" },
        { name: "有效期至", value: "proasgress" },
        {
          name: "店员状态",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "男" : "女");
          },
          filter: [{ text: "男", value: "1" }, { text: "女", value: "2" }]
        },
        { name: "店员二维码", value: "img", img: true, download: true }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          address: "哈哈 1518 弄",
          progress: 10,
          switch: true,
          img:
            "https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1546510062&di=e4f7d8cc8a9454427b3a469d7d78a6f2&src=http://img17.3lian.com/201612/20/37494c35be42137c1fb6b5b8e6912f44.png"
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          address: "哈哈 1517 弄",
          progress: 30,
          switch: true,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329861&di=1ddde06eed673a9d016fdf5c71ce2f68&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201609%2F18%2F20160918132951_vweJn.jpeg"
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          address: "哈哈 1519 弄",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          address: "哈哈 1516 弄",
          progress: 90,
          switch: true,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329858&di=e19621b4b431c3bea0a4ed1e91c69bdd&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201509%2F09%2F20150909173736_kvfYC.jpeg"
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          address: "哈哈 1516 弄",
          progress: 100,
          switch: false,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329858&di=c04bf34a390ca5a9a015083034740eeb&imgtype=0&src=http%3A%2F%2Fpic31.photophoto.cn%2F20140405%2F0005018780796587_b.jpg"
        }
      ],

      tableOperate: [
        {
          name: "编辑",
          value: "edit",
          show: function(e) {
            return true;
          }
        },
        {
          name: "删除",
          value: "delete",
          show: function(e) {
            return true;
          }
        }
      ]
    };
  },
  // 注册组件
  components: {
    clerkEdit
  },
  methods: {
    searchEvent(e) {
      console.log(e);
    },
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    export(e) {
      this.$exportData(this.tableHead, this.tableData, this.tempName);
      // this.$refs.tableBase.butEvent("export");
    },
    import(e) {
      console.log("import");
      this.dialogImportVisible = true;
    },
    edit(e) {
      console.log(e);
      this.formData = e || {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: ""
      };
      this.dialogFormVisible = true;
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },

    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    },

    // Excel
    downTempExcel(e) {
      console.log("downTempExcel");
      this.$exportData(this.tableHead, [], this.tempName);
    },
    importTempExcel(e) {
      console.log("importTempExcel");
      // const excelData = this.$importData(file);
      const fileReader = new FileReader();
      fileReader.onload = ev => {
        const data = ev.target.result;
        const workbook = XLSX.read(data, {
          type: "binary"
        });
        for (let sheet in workbook.Sheets) {
          const sheetArray = XLSX.utils.sheet_to_json(workbook.Sheets[sheet]);
          console.log(sheetArray);
        }
      };
      fileReader.readAsBinaryString(e.raw);
    }
  }
};
</script>
<style lang="scss" scoped>
</style>



