<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>售卡分佣规则</div>
    </div>
    <div class="mainTable_swith">
      <el-row>
        <el-col :span="20">
          <div>温馨提示: 开启后, 门店, 店员可享受指定分佣活动. 关闭后, 门店, 店员将不享受任何分佣活动.</div>
        </el-col>
        <el-col :span="4">
          <div class="fr txt">分佣活动
            <el-switch v-model="switchData.delivery"></el-switch>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="mainTable_content">
      <table-base
        :tableBut="tableBut"
        :tableSelection="tableSelection"
        :tableExpand="tableExpand"
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableOperate="tableOperate"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        @butEvent="butEvent"
        @operateEvent="operateEvent"
        @switchEvent="switchEvent"
        @sortEvent="sortEvent"
        @selectionEvent="selectionEvent"
        @filterEvent="filterEvent"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
    <div class="mainTable_dialog">
      <el-dialog
        width="1000px"
        title="分佣规则"
        top="10vh"
        v-if="dialogFormVisible"
        :visible.sync="dialogFormVisible"
        :close-on-click-modal="false"
      >
        <com-edit :formData="formData" @cancelForm="cancelForm" @click="submitForm"></com-edit>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import comEdit from "./comEdit.vue";
export default {
  name: "Commission",
  data() {
    return {
      moneyList: [100, 200, 500, 800],
      switchData: {},
      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      tableBut: [{ name: "新 增", value: "edit", show: true }],
      tableHead: [
        { name: "规则名称", value: "name" },
        { name: "所属品牌", value: "date2", sort: true },
        {
          name: "门店属性",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "直营店" : "加盟/联营");
          },
          filter: [
            { text: "直营店", value: "1" },
            { text: "加盟/联营", value: "2" }
          ]
        },
        // { name: "免单倍数", value: "name" },
        {
          name: "状态",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "已开启" : "已暂停");
          },
          filter: [
            { text: "已开启", value: "1" },
            { text: "已暂停", value: "2" },
            { text: "已过期", value: "3" }
          ]
        },
        { name: "创建时间", value: "date", sort: true }
      ],
      tableData: [
        {
          id: 1,
          date2: "2016-05-02",
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          switch: true
        },
        {
          id: 2,
          date2: "2016-05-02",
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          progress: 30,
          switch: true
        },
        {
          id: 3,
          date2: "2016-05-02",
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date2: "2016-05-02",
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          progress: 90,
          switch: true
        },
        {
          id: 5,
          date2: "2016-05-02",
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          progress: 100,
          switch: false
        }
      ],
      tableOperate: [
        {
          name: "开启",
          value: "open",
          show: function(e) {
            return e.status != 1 ? true : false;
          }
        },
        {
          name: "关闭",
          value: "close",
          show: function(e) {
            return e.status == 1 ? true : false;
          }
        },
        {
          name: "编辑",
          value: "edit",
          show: function(e) {
            return true;
          }
        },
        {
          name: "删除",
          value: "delete",
          show: function(e) {
            return true;
          }
        }
      ]
    };
  },
  // 注册组件
  components: {
    comEdit
  },
  methods: {
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    sortEvent(e) {
      console.log(e);
    },
    switchEvent(e) {
      console.log(e.name);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    import(e) {
      console.log("import");
    },
    export(e) {
      console.log("export");
    },
    open(e) {
      console.log(e.name);
    },
    close(e) {
      console.log(e.name);
    },
    edit(e) {
      this.formData = e || {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: ""
      };
      this.dialogFormVisible = true;
    },
    delete(e) {
      if (e) {
        console.log(e.name);
      } else {
        console.log("批量删除");
      }
    },
    check(e) {
      console.log(e.name);
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.el-row {
  line-height: 40px;
}
.mainTable_swith {
  margin-bottom: 10px;
  .txt {
    font-size: 15px;
    font-weight: bold;
    .el-switch {
      margin-left: 20px;
    }
  }
}
</style>


