<template>
  <div class="dialogBox sizeInline">
    <el-form
      inline
      :model="formData"
      :rules="rules"
      size="medium"
      ref="formData"
      label-width="100px"
      label-position="right"
    >
      <el-form-item label="门店名称" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入门店名称" clearable></el-input>
      </el-form-item>
      <el-form-item label="门店编码" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入门店编码" clearable></el-input>
      </el-form-item>
      <el-form-item label="品牌门店" prop="store">
        <el-cascader
          v-model="formData.store"
          :options="storeOptions"
          @active-item-change="cascaderChange"
          clearable
        ></el-cascader>
      </el-form-item>
      <el-form-item label="地区" prop="store">
        <el-cascader
          v-model="formData.store"
          :options="storeOptions"
          @active-item-change="cascaderChange"
          clearable
        ></el-cascader>
      </el-form-item>
      <el-form-item label="详细地址" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入详细地址" clearable></el-input>
      </el-form-item>
      <el-form-item label="门店属性" prop="type">
        <el-radio-group v-model="formData.type" @change="typeRadio">
          <el-radio label="money">直营店</el-radio>
          <el-radio label="card">加盟店</el-radio>
          <el-radio label="card">联营店</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="客服电话" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入客服电话" clearable></el-input>
      </el-form-item>
      <el-form-item label="客服手机" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入客服手机" clearable></el-input>
      </el-form-item>
      <el-form-item label="门店联系人" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入门店联系人" clearable></el-input>
      </el-form-item>
      <el-form-item label="门店电话" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入门店电话" clearable></el-input>
      </el-form-item>
      <el-form-item label="门店邮箱" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入门店邮箱" clearable></el-input>
      </el-form-item>
      <el-form-item label="经度" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入经度" clearable></el-input>
      </el-form-item>
      <el-form-item label="纬度" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入纬度" clearable></el-input>
      </el-form-item>
      <el-form-item label=" ">
        <el-button type="primary" @click="submitForm('formData')" size="small" class="small">查询门店经纬度</el-button>
      </el-form-item>
      <el-form-item label="门店大图" prop="resource">
        <el-upload
          class="avatar-uploader"
          action="https://luckypay.slsy.com/web/api/upload"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          <div class="el-upload__text">添 加 图 片</div>
          <div class="el-upload__tip" slot="tip">大图建议尺寸 : 400*150, 小图建议尺寸 : 400*150, 不得超过2M.</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="门店小图" prop="resource">
        <el-upload
          class="avatar-uploader smallImg"
          action="https://luckypay.slsy.com/web/api/upload"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          <div class="el-upload__text">添 加 图 片</div>
          <!-- <div
            class="el-upload__tip"
            slot="tip"
          >图片建议尺寸 : 400*150, 不得超过2M, 图片格式为 : '.jpg, .gif, .png'.</div>-->
        </el-upload>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer ac">
      <el-button type="primary" @click="submitForm('formData')">确 认</el-button>
      <el-button @click="cancelForm">取 消</el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "editUser",
  data() {
    return {
      ruleArr: [{ name1: "", name2: "" }],
      // 验证
      rules: {
        name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        region: [
          { required: true, message: "请选择活动区域", trigger: "change" }
        ],
        store: [
          { required: true, message: "请选择品牌门店", trigger: "change" }
        ],
        time: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ]
      },

      // 级联动
      storeOptions: [
        {
          id: "1",
          value: "zhinan",
          label: "指南",
          children: [
            {
              id: "2",
              value: "shejiyuanze",
              label: "设计原则",
              children: [
                { id: "3", value: "yizhi", label: "一致" },
                { id: "4", value: "fankui", label: "反馈" },
                { id: "5", value: "xiaolv", label: "效率" },
                { id: "6", value: "kekong", label: "可控" }
              ]
            },
            {
              id: "7",
              value: "daohang",
              label: "导航",
              children: [
                { id: "8", value: "cexiangdaohang", label: "侧向导航" },
                { id: "9", value: "dingbudaohang", label: "顶部导航" }
              ]
            }
          ]
        },
        {
          id: "10",
          value: "zujian",
          label: "组件",
          children: [
            {
              id: "11",
              value: "basic",
              label: "Basic",
              children: [
                { id: "12", value: "layout", label: "Layout 布局" },
                { id: "13", value: "color", label: "Color 色彩" },
                { id: "14", value: "typography", label: "Typography 字体" },
                { id: "15", value: "icon", label: "Icon 图标" },
                { id: "16", value: "button", label: "Button 按钮" }
              ]
            },
            {
              id: "17",
              value: "form",
              label: "Form",
              children: [
                { id: "18", value: "radio", label: "Radio 单选框" },
                { id: "19", value: "checkbox", label: "Checkbox 多选框" },
                { id: "20", value: "input", label: "Input 输入框" },
                {
                  id: "21",
                  value: "input-number",
                  label: "InputNumber 计数器"
                },
                { id: "22", value: "select", label: "Select 选择器" },
                { id: "23", value: "cascader", label: "Cascader 级联选择器" },
                { id: "24", value: "switch", label: "Switch 开关" },
                { id: "25", value: "slider", label: "Slider 滑块" },
                {
                  id: "26",
                  value: "time-picker",
                  label: "TimePicker 时间选择器"
                },
                {
                  id: "27",
                  value: "date-picker",
                  label: "DatePicker 日期选择器"
                },
                {
                  id: "28",
                  value: "datetime-picker",
                  label: "DateTimePicker 日期时间选择器"
                },
                { id: "29", value: "upload", label: "Upload 上传" },
                { id: "30", value: "rate", label: "Rate 评分" },
                { id: "31", value: "form", label: "Form 表单" }
              ]
            },
            {
              id: "32",
              value: "data",
              label: "Data",
              children: [
                { id: "33", value: "table", label: "Table 表格" },
                { id: "34", value: "tag", label: "Tag 标签" },
                { id: "35", value: "progress", label: "Progress 进度条" },
                { id: "36", value: "tree", label: "Tree 树形控件" },
                { id: "37", value: "pagination", label: "Pagination 分页" },
                { id: "38", value: "badge", label: "Badge 标记" }
              ]
            },
            {
              id: "39",
              value: "notice",
              label: "Notice",
              children: [
                { id: "40", value: "alert", label: "Alert 警告" },
                { id: "41", value: "loading", label: "Loading 加载" },
                { id: "42", value: "message", label: "Message 消息提示" },
                { id: "43", value: "message-box", label: "MessageBox 弹框" },
                { id: "44", value: "notification", label: "Notification 通知" }
              ]
            },
            {
              id: "45",
              value: "navigation",
              label: "Navigation",
              children: [
                { id: "46", value: "menu", label: "NavMenu 导航菜单" },
                { id: "47", value: "tabs", label: "Tabs 标签页" },
                { id: "48", value: "breadcrumb", label: "Breadcrumb 面包屑" },
                { id: "49", value: "dropdown", label: "Dropdown 下拉菜单" },
                { id: "50", value: "steps", label: "Steps 步骤条" }
              ]
            },
            {
              id: "51",
              value: "others",
              label: "Others",
              children: [
                { id: "52", value: "dialog", label: "Dialog 对话框" },
                { id: "53", value: "tooltip", label: "Tooltip 文字提示" },
                { id: "54", value: "popover", label: "Popover 弹出框" },
                { id: "55", value: "card", label: "Card 卡片" },
                { id: "56", value: "carousel", label: "Carousel 走马灯" },
                { id: "57", value: "collapse", label: "Collapse 折叠面板" }
              ]
            }
          ]
        },
        {
          id: "58",
          value: "ziyuan",
          label: "资源",
          children: [
            { id: "59", value: "axure", label: "Axure Components" },
            { id: "60", value: "sketch", label: "Sketch Templates" },
            { id: "61", value: "jiaohu", label: "组件交互文档" }
          ]
        }
      ],
      // 大图路径
      imageUrl: "",
      // 小图
      dialogImageUrl: "",
      dialogCardList: false
    };
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    typeRadio(e) {
      console.log(e);
    },
    buyRadio(e) {
      console.log(e);
    },
    giveRadio(e) {
      console.log(e);
    },
    addRuls() {
      this.ruleArr.push({ name1: "", name2: "" });
    },
    delRuls(e) {
      if (this.ruleArr.length > 1) {
        this.ruleArr.splice(e, 1);
      }
    },
    // 初始化
    init() {},
    // 级联动
    cascaderChange(val) {
      console.log(val);
    },
    // 上传大图
    sucImgB(res, file) {
      console.log(res);
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeImgB(file) {
      console.log(file);
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 上传小图
    sucImgS() {},
    beforeImgS() {},
    remImgS(file, fileList) {
      console.log(file, fileList);
    },
    // 树形控件
    treeClick(e) {
      console.log(this.$refs.tree.getCheckedKeys());
    },

    // 确认
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.formData);
        console.log(valid);
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });

      this.$emit("submitForm");
    },
    // 取消
    cancelForm() {
      this.$emit("cancelForm");
    }
  }
};
</script>
<style lang="scss" scoped>
.el-dialog .el-dialog__body .sizeInline {
  .el-form--inline .el-form-item {
    margin-right: 30px;
  }
  .smallImg {
    position: relative;
    .avatar-uploader-icon {
      width: 150px;
      height: 150px;
    }
    .el-upload__tip {
      position: absolute;
      top: 10px;
      left: 170px;
      width: 240px;
    }
  }
}
</style>




