import Vue from 'vue'
import axios from 'axios'
// import router from './router'
// import './plugins/element.js'
import {
  Loading
} from 'element-ui';

// axios.defaults.baseURL = 'https://luckypay.slsy.com/web/api';
axios.defaults.timeout = 5000;
axios.defaults.headers = {
  "X-Requested-With": "XMLHttpRequest"
};

// http request 拦截器
axios.interceptors.request.use(
  config => {
    return config
  },
  err => {
    return Promise.reject(err)
  })

// http response 拦截器
axios.interceptors.response.use(
  response => {
    // 请求成功
    if (response.data.code == 20000) {
      return response
    }
    // 参数异常
    else if (response.data.code == 40001) {
      return Vue.prototype.$message.error({
        title: '错误信息提示',
        duration: 2500,
        message: response.data.msg
      });
    }
    // 系统异常
    else if (response.data.code == 1002) {
      return Vue.prototype.$notify.error({
        title: '错误信息提示',
        duration: 2500,
        message: response.data.desc
      });
    }
    // 用户未登录
    else if (response.data.code == 1003) {
      //导至登录页
      // return router.push('/login');
    }
    // 失败
    else if (response.data.code == 1004) {
      // 显示请求失败的错误信息
      return Vue.prototype.$notify.error({
        title: '错误信息提示',
        duration: 2500,
        message: response.data.desc
      });
    }
    // 权限不足
    else if (response.data.code == 1009) {
      return Vue.prototype.$notify.error({
        title: '错误信息提示',
        duration: 2500,
        message: response.data.desc
      });
    }

  },
  err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          console.log('错误请求')
          break;
        case 401:
          console.log('未授权，请重新登录')
          break;
        case 403:
          console.log('拒绝访问')
          break;
        case 404:
          console.log('请求错误,未找到该资源')
          break;
        case 405:
          console.log('请求方法未允许')
          break;
        case 408:
          console.log('请求超时')
          break;
        case 500:
          console.log('服务器端出错')
          break;
        case 501:
          console.log('网络未实现')
          break;
        case 502:
          console.log('网络错误')
          break;
        case 503:
          console.log('服务不可用')
          break;
        case 504:
          console.log('网络超时')
          break;
        case 505:
          console.log('http版本不支持该请求')
          break;
        default:
          console.log(`连接错误${err.response.status}`)
      }
    } else {
      console.log('连接到服务器失败')
    }
    return Promise.resolve(err.response)
    return Promise.reject(err)
  })

const req = (method, url, params) => {
  return axios({
    method: method,
    url: url,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      token: localStorage.getItem('logintoken')
    },
    data: params,
    traditional: true,
    transformRequest: [
      function (data) {
        let ret = ''
        for (let it in data) {
          ret +=
            encodeURIComponent(it) +
            '=' +
            encodeURIComponent(data[it]) +
            '&'
        }
        return ret
      }
    ]
  }).then(res => res.data);
};

export default {
  get: function (url, params, config) {
    return req("GET", url, params, config);
  },

  post: function (url, params, config) {
    return req("POST", url, params, config);
  },

  delete: function (url, params, config) {
    return req("DELETE", url, params, config);
  },

  put: function (url, params, config) {
    return req("PUT", url, params, config);
  }
};



