<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>售卡业绩统计</div>
    </div>
    <el-tabs tab-position="left" @tab-click="tabClick" value="store" stretch>
      <el-tab-pane name="store" label="门店业绩">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableOperate="tableOperate"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
      <el-tab-pane name="clerk" label="店员业绩">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableOperate="tableOperate"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
    </el-tabs>

    <div class="mainTable_dialog">
      <el-dialog
        width="1280px"
        title="业绩统计"
        top="10vh"
        v-if="dialogFormVisible"
        :visible.sync="dialogFormVisible"
        :close-on-click-modal="false"
      >
        <achi-check :formData="formData"></achi-check>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import achiCheck from "./achiCheck.vue";
export default {
  name: "Achievement",
  data() {
    return {
      type: "",
      tempName: "",
      searchData: {},

      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      searchList: [
        {
          id: "select",
          type: "select",
          name: "select",
          title: "门店类型",
          placeholder: "请选择门店类型",
          options: [
            {
              name: "lll",
              value: "ooo"
            }
          ]
        },
        {
          id: "date",
          type: "timeDateSim",
          name: "date",
          title: "结算月份",
          placeholder: "请选择",
          timeType: "date"
        },
        {
          id: "cascader",
          type: "cascader",
          name: "cascader",
          title: "品牌门店",
          placeholder: "请选择品牌门店"
        }
      ],

      tableBut: [{ name: "导 出", value: "export", show: true }],
      tableHead: [
        { name: "所属品牌", value: "name" },
        { name: "门店名称", value: "date" },
        {
          name: "门店类型",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "男" : "女");
          },
          filter: [{ text: "男", value: "1" }, { text: "女", value: "2" }]
        },
        { name: "售卡总额", value: "address" },
        {
          name: "门店分佣",
          value: "progress"
        },
        {
          name: "售卡总分佣",
          value: "id"
        }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          address: "上海 1518 弄",
          progress: 10,
          switch: true,
          img:
            "https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1546510062&di=e4f7d8cc8a9454427b3a469d7d78a6f2&src=http://img17.3lian.com/201612/20/37494c35be42137c1fb6b5b8e6912f44.png"
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          address: "上海 1517 弄",
          progress: 30,
          switch: true,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329861&di=1ddde06eed673a9d016fdf5c71ce2f68&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201609%2F18%2F20160918132951_vweJn.jpeg"
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          address: "上海 1519 弄",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          address: "上海 1516 弄",
          progress: 90,
          switch: true,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329858&di=e19621b4b431c3bea0a4ed1e91c69bdd&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201509%2F09%2F20150909173736_kvfYC.jpeg"
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          address: "上海 1516 弄",
          progress: 100,
          switch: false,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329858&di=c04bf34a390ca5a9a015083034740eeb&imgtype=0&src=http%3A%2F%2Fpic31.photophoto.cn%2F20140405%2F0005018780796587_b.jpg"
        }
      ],

      tableOperate: [
        {
          name: "查看",
          value: "check",
          show: function(e) {
            return true;
          }
        }
      ]
    };
  },
  // 注册组件
  components: {
    achiCheck
  },
  methods: {
    tabClick(e) {
      this.type = e.name;
      console.log(this.type);
      if (e.name == "clerk") {
        this.searchList = [
          {
            id: "input",
            type: "input",
            name: "input",
            title: "店员姓名",
            placeholder: "请输入店员姓名"
          },
          {
            id: "input",
            type: "input",
            name: "input",
            title: "员工工号",
            placeholder: "请输入员工工号"
          },
          {
            id: "cascader",
            type: "cascader",
            name: "cascader",
            title: "品牌门店",
            placeholder: "请选择品牌门店"
          },
          {
            id: "input",
            type: "input",
            name: "input",
            title: "员工角色",
            placeholder: "请输入员工角色"
          },
          {
            id: "select",
            type: "select",
            name: "select",
            title: "店员状态",
            placeholder: "店员状态",
            options: [
              {
                name: "lll",
                value: "ooo"
              }
            ]
          },
          {
            id: "date",
            type: "timeDateSim",
            name: "date",
            title: "结算月份",
            placeholder: "请选择",
            timeType: "date"
          }
        ];
        this.tableHead = [
          { name: "店员", value: "name" },
          { name: "店员工号", value: "date" },
          { name: "品牌", value: "name2" },
          { name: "门店", value: "date2" },
          {
            name: "角色",
            value: "status",
            format: (row, column, cellValue, index) => {
              return (cellValue = cellValue == 1 ? "男" : "女");
            },
            filter: [{ text: "男", value: "1" }, { text: "女", value: "2" }]
          },
          {
            name: "店员状态",
            value: "status",
            format: (row, column, cellValue, index) => {
              return (cellValue = cellValue == 1 ? "男" : "女");
            },
            filter: [{ text: "男", value: "1" }, { text: "女", value: "2" }]
          },
          { name: "售卡总额", value: "address" },
          {
            name: "已解冻售卡分佣",
            value: "progress"
          },
          {
            name: "冻结中售卡分佣",
            value: "id"
          }
        ];
      } else {
        this.searchList = [
          {
            id: "select",
            type: "select",
            name: "select",
            title: "门店类型",
            placeholder: "请选择门店类型",
            options: [
              {
                name: "lll",
                value: "ooo"
              }
            ]
          },
          {
            id: "date",
            type: "timeDateSim",
            name: "date",
            title: "结算月份",
            placeholder: "请选择",
            timeType: "date"
          },
          {
            id: "cascader",
            type: "cascader",
            name: "cascader",
            title: "品牌门店",
            placeholder: "请选择品牌门店"
          }
        ];
        this.tableHead = [
          { name: "所属品牌", value: "name" },
          { name: "门店名称", value: "date" },
          {
            name: "门店类型",
            value: "status",
            format: (row, column, cellValue, index) => {
              return (cellValue = cellValue == 1 ? "男" : "女");
            },
            filter: [{ text: "男", value: "1" }, { text: "女", value: "2" }]
          },
          { name: "售卡总额", value: "address" },
          {
            name: "门店分佣",
            value: "progress"
          },
          {
            name: "售卡总分佣",
            value: "id"
          }
        ];
      }
    },

    searchEvent(e) {
      console.log(e);
    },
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    export(e) {
      console.log("export");
      let tempName = this.type == "clerk" ? "店员业绩" : "门店业绩";
      this.$exportData(this.tableHead, this.tableData, tempName);
    },
    check(e) {
      console.log(e);

      this.dialogFormVisible = true;
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },

    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
</style>



