<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>{{tempName}}</div>
    </div>
    <div class="mainTable_search">
      <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
    </div>
    <div class="mainTable_content">
      <table-base
        :tableBut="tableBut"
        :tableSelection="tableSelection"
        :tableExpand="tableExpand"
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableOperate="tableOperate"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        @butEvent="butEvent"
        @operateEvent="operateEvent"
        @selectionEvent="selectionEvent"
        @filterEvent="filterEvent"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
  </div>
</template>
<script>
export default {
  name: "UserInfo",
  data() {
    return {
      
      tempName: "礼品卡转赠",
      searchData: {},

      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      searchList: [
        {
          id: "input",
          type: "input",
          name: "input",
          title: "转赠手机",
          placeholder: "请输入转赠手机号"
        },
        {
          id: "input",
          type: "input",
          name: "input",
          title: "礼品卡号",
          placeholder: "请输入礼品卡号"
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "转赠时间",
          bplaceholder: "开始时间",
          eplaceholder: "结束时间",
          timeType: "daterange"
        },
        {
          id: "input",
          type: "input",
          name: "input",
          title: "领取手机",
          placeholder: "请输入领取手机号"
        },
        {
          id: "select",
          type: "select",
          name: "select",
          title: "领卡状态",
          placeholder: "请选择领卡状态"
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "领取时间",
          bplaceholder: "开始时间",
          eplaceholder: "结束时间",
          timeType: "datetimerange"
        }
      ],

      tableBut: [{ name: "导 出", value: "export", show: true }],
      tableHead: [
        { name: "转增人", value: "name" },
        { name: "转赠人手机号", value: "progress" },
        { name: "转赠时间", value: "date" },
        { name: "礼品卡号", value: "progress" },
        { name: "礼品卡金额 (元)", value: "progress" },
        {
          name: "领取状态",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "男" : "女");
          },
          filter: [{ text: "男", value: "1" }, { text: "女", value: "2" }]
        },
        { name: "领取人", value: "address" },
        { name: "领取人手机号", value: "address" },
        { name: "转赠时间", value: "date" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          address: "上海市普陀",
          progress: 10,
          switch: true,
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          address: "上海市普陀",
          progress: 30,
          switch: true,
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          address: "上海市普江路 1519 弄",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          address: "上海市普江路 1516 弄",
          progress: 90,
          switch: true,
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          address: "上海市普江路 1516 弄",
          progress: 100,
          switch: false,
        }
      ]
    };
  },
  // 注册组件
  components: {
  },
  methods: {
    searchEvent(e) {
      console.log(e);
    },
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    export(e) {
      console.log("export");
      this.$exportData(this.tableHead, this.tableData, this.tempName);
    },
    check(e) {
      this.formData = e || {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: ""
      };
      this.dialogFormVisible = true;
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },

    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
</style>



