<template>
  <div class="dialogBox">
    <el-form
      :model="formData"
      :rules="rules"
      size="medium"
      ref="formData"
      label-width="85px"
      label-position="right"
    >
      <el-form-item label="区域名称" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入区域名称" clearable></el-input>
      </el-form-item>
      <el-form-item label="区域编码" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入区域编码" clearable></el-input>
      </el-form-item>
      <el-form-item label="负责人" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入负责人" clearable></el-input>
      </el-form-item>
      <el-form-item label="联系电话" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入联系电话" clearable></el-input>
      </el-form-item>
      <el-form-item label="所属品牌" prop="region">
        <el-select v-model="formData.region" placeholder="请选择活动区域" clearable>
          <el-option label="区域一" value="shanghai"></el-option>
          <el-option label="区域二" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="区域介绍" prop="desc">
        <el-input
          type="textarea"
          resize="none"
          style="width:400px;"
          :autosize="{ minRows: 4, maxRows: 6}"
          v-model="formData.desc"
          placeholder="请输入区域介绍"
        ></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer ac">
      <el-button type="primary" @click="submitForm('formData')">确 认</el-button>
      <el-button @click="cancelForm">取 消</el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "areaEdit",
  data() {
    return {
      ruleArr: [{ name1: "", name2: "" }],
      // 验证
      rules: {
        name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        region: [
          { required: true, message: "请选择活动区域", trigger: "change" }
        ],
        store: [
          { required: true, message: "请选择品牌门店", trigger: "change" }
        ],
        time: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ]
      }
    };
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    typeRadio(e) {
      console.log(e);
    },
    buyRadio(e) {
      console.log(e);
    },
    giveRadio(e) {
      console.log(e);
    },
    addRuls() {
      this.ruleArr.push({ name1: "", name2: "" });
    },
    delRuls(e) {
      if (this.ruleArr.length > 1) {
        this.ruleArr.splice(e, 1);
      }
    },
    // 初始化
    init() {},
    // 级联动
    cascaderChange(val) {
      console.log(val);
    },
    // 上传大图
    sucImgB(res, file) {
      console.log(res);
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeImgB(file) {
      console.log(file);
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 上传小图
    sucImgS() {},
    beforeImgS() {},
    remImgS(file, fileList) {
      console.log(file, fileList);
    },
    // 树形控件
    treeClick(e) {
      console.log(this.$refs.tree.getCheckedKeys());
    },

    // 确认
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.formData);
        console.log(valid);
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });

      this.$emit("submitForm");
    },
    // 取消
    cancelForm() {
      this.$emit("cancelForm");
    }
  }
};
</script>
<style lang="scss" scoped>
.el-dialog .el-dialog__body .avatar-uploader {
  position: relative;
  .avatar-uploader-icon {
    width: 150px;
    height: 150px;
    text-align: center;
    font-size: 25px;
    line-height: 120px;
  }
  .el-upload__tip {
    position: absolute;
    top: 10px;
    left: 170px;
    width: 240px;
  }
}
</style>




