<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>礼品卡消费结算</div>
    </div>

    <el-tabs tab-position="left" @tab-click="tabClick" value="day" stretch>
      <el-tab-pane name="day" label="日结算">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableOperate="tableOperate"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
      <el-tab-pane name="month" label="月结算">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableOperate="tableOperate"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
    </el-tabs>

    <div class="mainTable_dialog">
      <el-dialog
        width="750px"
        title="查看消费日结算明细"
        top="10vh"
        v-if="dialogFormVisible"
        :visible.sync="dialogFormVisible"
        :close-on-click-modal="false"
      >
        <settle-check :formData="formData"></settle-check>
      </el-dialog>

      <el-dialog title="礼品卡消费日结算" :visible.sync="setDateBolDay" v-if="setDateBolDay" width="420px">
        <div class="dialogBox">
          <el-form
            :model="setDate"
            :rules="rules"
            size="medium"
            ref="setDate"
            label-width="100px"
            label-position="right"
          >
            <el-form-item label="选择日期" prop="name">
              <el-date-picker
                style="width:250px;"
                v-model="balanceDate"
                size="medium"
                placeholder="item.placeholder"
                type="date"
                clearable
              ></el-date-picker>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer ac">
            <el-button size="small" class="small" type="primary" @click="submitForm('setDate')">确 认</el-button>
            <el-button size="small" class="small" @click="cancelForm">取消</el-button>
          </div>
        </div>
      </el-dialog>

      <el-dialog
        title="礼品卡消费月结算"
        :visible.sync="setDateBolMonth"
        v-if="setDateBolMonth"
        width="420px"
      >
        <div class="dialogBox">
          <el-form
            :model="setDate"
            :rules="rules"
            size="medium"
            ref="setDate"
            label-width="100px"
            label-position="right"
          >
            <el-form-item label="选择月份" prop="name">
              <el-date-picker
                style="width:250px"
                v-model="balanceDate"
                size="medium"
                placeholder="item.placeholder"
                type="month"
                clearable
              ></el-date-picker>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer ac">
            <el-button size="small" class="small" type="primary" @click="submitForm('setDate')">确 认</el-button>
            <el-button size="small" class="small" @click="cancelForm">取消</el-button>
          </div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import settleCheck from "./settleCheck.vue";
export default {
  name: "DailySettle",
  data() {
    return {
      type: "day",
      searchData: {},
      balanceDate: "",
      formData: {},
      dialogFormVisible: false,
      setDateBolDay: false,
      setDateBolMonth: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      searchList: [
        {
          id: "cascader",
          type: "cascader",
          name: "cascader",
          title: "品牌门店",
          placeholder: "请选择品牌门店"
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "交易日期",
          bplaceholder: "开始时间",
          eplaceholder: "结束时间",
          timeType: "daterange"
        }
      ],
      tableBut: [
        { name: "结算管理", value: "setDate", show: true },
        { name: "导 出", value: "export", show: true }
      ],
      tableHead: [
        { name: "品牌", value: "progress" },
        { name: "门店名称", value: "name" },
        { name: "门店类型", value: "date" },
        { name: "礼品卡金额消费 (元)", value: "address" },
        // { name: "免单金额消费 (元)", value: "address" },
        // { name: "实际免单金额消费 (元)", value: "address" },
        { name: "赠送金额消费 (元)", value: "address" },
        { name: "结算时间", value: "date" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          address: " 1518 弄",
          progress: 10,
          switch: true,
          img:
            "https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1546510062&di=e4f7d8cc8a9454427b3a469d7d78a6f2&src=http://img17.3lian.com/201612/20/37494c35be42137c1fb6b5b8e6912f44.png"
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          address: " 1517 弄",
          progress: 30,
          switch: true,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329861&di=1ddde06eed673a9d016fdf5c71ce2f68&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201609%2F18%2F20160918132951_vweJn.jpeg"
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          address: " 1519 弄",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          address: " 1516 弄",
          progress: 90,
          switch: true,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329858&di=e19621b4b431c3bea0a4ed1e91c69bdd&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201509%2F09%2F20150909173736_kvfYC.jpeg"
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          address: " 1516 弄",
          progress: 100,
          switch: false,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329858&di=c04bf34a390ca5a9a015083034740eeb&imgtype=0&src=http%3A%2F%2Fpic31.photophoto.cn%2F20140405%2F0005018780796587_b.jpg"
        }
      ],

      tableOperate: [
        {
          name: "查看",
          value: "check",
          show: function(e) {
            return true;
          }
        },
        {
          name: "确认结算",
          value: "check",
          show: function(e) {
            return true;
          }
        },
        {
          name: "删除",
          value: "check",
          show: function(e) {
            return true;
          }
        }
      ]
    };
  },
  // 注册组件
  components: {
    settleCheck
  },
  methods: {
    tabClick(e) {
      console.log(e.name);
      this.type = e.name;
    },
    // 设置结算日期
    setDate() {
      if (this.type == "day") {
        this.setDateBolDay = true;
      } else {
        this.setDateBolMonth = true;
      }
    },

    searchEvent(e) {
      console.log(e);
    },
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    export(e) {
      console.log("export");
    },
    check(e) {
      this.formData = e || {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: ""
      };
      this.dialogFormVisible = true;
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },

    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
</style>




