<template>
  <div class="searchBox">
    <el-row :gutter="0" class="serBox">
      <div v-for="item in searchList" :key="item.value">
        <!-- 输入框 -->
        <el-col :span="6" v-if="item.type=='input'">
          <label :for="item.id">{{item.title}} :</label>
          <el-input
            :id="item.id"
            size="medium"
            v-model="searchData[item.name]"
            :placeholder="item.placeholder"
            clearable
          ></el-input>
        </el-col>
        <!-- 选择框 -->
        <el-col :span="6" v-if="item.type=='select'">
          <label :for="item.id">{{item.title}} :</label>
          <el-select
            :id="item.id"
            size="medium"
            v-model="searchData[item.name]"
            :placeholder="item.placeholder"
            clearable
          >
            <el-option
              v-for="opt in item.options"
              :key="opt.value"
              :label="opt.name"
              :value="opt.value"
            ></el-option>
          </el-select>
        </el-col>
        <!-- 级联动 -->
        <el-col :span="8" v-if="item.type=='cascader'">
          <label :for="item.id">{{item.title}} :</label>
          <el-cascader
            :id="item.id"
            size="medium"
            change-on-select
            v-model="searchData[item.name]"
            :options="cascaderOptions"
            :placeholder="item.placeholder"
            @active-item-change="cascaderChange"
            clearable
          ></el-cascader>
        </el-col>
        <!-- 时间 -->
        <el-col :span="8" v-if="item.type=='time'">
          <label :for="item.id">{{item.title}} :</label>
          <el-time-picker
            :id="item.id"
            size="medium"
            is-range
            v-model="searchData[item.name]"
            range-separator="至"
            :start-placeholder="item.bplaceholder"
            :end-placeholder="item.eplaceholder"
            placeholder="选择时间范围"
          ></el-time-picker>
        </el-col>
        <!-- 日期时间 -->
        <!-- year/month/date/week/datetime/datetimerange/daterange -->
        <el-col :span="6" v-if="item.type=='timeDateSim'">
          <label :for="item.id">{{item.title}} :</label>
          <el-date-picker
            v-model="searchData[item.name]"
            :id="item.id"
            size="medium"
            :placeholder="item.placeholder"
            :type="item.timeType"
            clearable
          ></el-date-picker>
        </el-col>
        <el-col :span="9" v-if="item.type=='timeDate'">
          <label :for="item.id">{{item.title}} :</label>
          <el-date-picker
            align="right"
            range-separator="至"
            v-model="searchData[item.name]"
            :id="item.id"
            size="medium"
            :type="item.timeType"
            :start-placeholder="item.bplaceholder"
            :end-placeholder="item.eplaceholder"
            clearable
          ></el-date-picker>
        </el-col>
      </div>
      <el-button
        class="butNormal serBut"
        type="primary"
        size="medium"
        @click="searchEvent"
        icon="el-icon-search"
      >搜 索</el-button>
    </el-row>
  </div>
</template>
<script>
export default {
  name: "searchbase",
  data() {
    return {
      // 级联动
      cascaderOptions: [
        {
          id: "1",
          value: "zhinan",
          label: "指南",
          children: [
            {
              id: "2",
              value: "shejiyuanze",
              label: "设计原则",
              children: [
                { id: "3", value: "yizhi", label: "一致" },
                { id: "4", value: "fankui", label: "反馈" },
                { id: "5", value: "xiaolv", label: "效率" },
                { id: "6", value: "kekong", label: "可控" }
              ]
            },
            {
              id: "7",
              value: "daohang",
              label: "导航",
              children: [
                { id: "8", value: "cexiangdaohang", label: "侧向导航" },
                { id: "9", value: "dingbudaohang", label: "顶部导航" }
              ]
            }
          ]
        },
        {
          id: "10",
          value: "zujian",
          label: "组件",
          children: [
            {
              id: "11",
              value: "basic",
              label: "Basic",
              children: [
                { id: "12", value: "layout", label: "Layout 布局" },
                { id: "13", value: "color", label: "Color 色彩" },
                { id: "14", value: "typography", label: "Typography 字体" },
                { id: "15", value: "icon", label: "Icon 图标" },
                { id: "16", value: "button", label: "Button 按钮" }
              ]
            },
            {
              id: "17",
              value: "form",
              label: "Form",
              children: [
                { id: "18", value: "radio", label: "Radio 单选框" },
                { id: "19", value: "checkbox", label: "Checkbox 多选框" },
                { id: "20", value: "input", label: "Input 输入框" },
                {
                  id: "21",
                  value: "input-number",
                  label: "InputNumber 计数器"
                },
                { id: "22", value: "select", label: "Select 选择器" },
                { id: "23", value: "cascader", label: "Cascader 级联选择器" },
                { id: "24", value: "switch", label: "Switch 开关" },
                { id: "25", value: "slider", label: "Slider 滑块" },
                {
                  id: "26",
                  value: "time-picker",
                  label: "TimePicker 时间选择器"
                },
                {
                  id: "27",
                  value: "date-picker",
                  label: "DatePicker 日期选择器"
                },
                {
                  id: "28",
                  value: "datetime-picker",
                  label: "DateTimePicker 日期时间选择器"
                },
                { id: "29", value: "upload", label: "Upload 上传" },
                { id: "30", value: "rate", label: "Rate 评分" },
                { id: "31", value: "form", label: "Form 表单" }
              ]
            },
            {
              id: "32",
              value: "data",
              label: "Data",
              children: [
                { id: "33", value: "table", label: "Table 表格" },
                { id: "34", value: "tag", label: "Tag 标签" },
                { id: "35", value: "progress", label: "Progress 进度条" },
                { id: "36", value: "tree", label: "Tree 树形控件" },
                { id: "37", value: "pagination", label: "Pagination 分页" },
                { id: "38", value: "badge", label: "Badge 标记" }
              ]
            },
            {
              id: "39",
              value: "notice",
              label: "Notice",
              children: [
                { id: "40", value: "alert", label: "Alert 警告" },
                { id: "41", value: "loading", label: "Loading 加载" },
                { id: "42", value: "message", label: "Message 消息提示" },
                { id: "43", value: "message-box", label: "MessageBox 弹框" },
                { id: "44", value: "notification", label: "Notification 通知" }
              ]
            },
            {
              id: "45",
              value: "navigation",
              label: "Navigation",
              children: [
                { id: "46", value: "menu", label: "NavMenu 导航菜单" },
                { id: "47", value: "tabs", label: "Tabs 标签页" },
                { id: "48", value: "breadcrumb", label: "Breadcrumb 面包屑" },
                { id: "49", value: "dropdown", label: "Dropdown 下拉菜单" },
                { id: "50", value: "steps", label: "Steps 步骤条" }
              ]
            },
            {
              id: "51",
              value: "others",
              label: "Others",
              children: [
                { id: "52", value: "dialog", label: "Dialog 对话框" },
                { id: "53", value: "tooltip", label: "Tooltip 文字提示" },
                { id: "54", value: "popover", label: "Popover 弹出框" },
                { id: "55", value: "card", label: "Card 卡片" },
                { id: "56", value: "carousel", label: "Carousel 走马灯" },
                { id: "57", value: "collapse", label: "Collapse 折叠面板" }
              ]
            }
          ]
        },
        {
          id: "58",
          value: "ziyuan",
          label: "资源",
          children: [
            { id: "59", value: "axure", label: "Axure Components" },
            { id: "60", value: "sketch", label: "Sketch Templates" },
            { id: "61", value: "jiaohu", label: "组件交互文档" }
          ]
        }
      ],
      searchData: {}
    };
  },

  props: ["searchList"],
  methods: {
    // 级联动
    cascaderChange(val) {
      console.log(val);
    },

    // 表格顶部按钮
    searchEvent() {
      this.$emit("searchEvent", this.searchData);
    }
  }
};
</script>
<style lang="scss" scoped>
</style>



