<template>
  <el-container>
    <el-header class="topHead">
      <div class="logoImg fl"></div>
      <div class="handle fr">
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link">
            <i class="icon-tab_admin icon iconfont"></i>
            admin
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="changePassword">修改密码</el-dropdown-item>
            <el-dropdown-item command="Logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <el-dialog
        class="userPass"
        title="修改密码"
        :visible.sync="dialogFormVisible"
        v-if="dialogFormVisible"
        width="420px"
      >
        <div class="dialogBox">
          <el-form
            :model="userPass"
            :rules="rules"
            size="medium"
            ref="userPass"
            label-width="100px"
            label-position="right"
          >
            <el-form-item label="旧密码" prop="name">
              <el-input style="width:250px" v-model="userPass.name" placeholder="请输入旧密码" clearable></el-input>
            </el-form-item>
            <el-form-item label="新密码" prop="name">
              <el-input style="width:250px" v-model="userPass.name" placeholder="请输入新密码" clearable></el-input>
            </el-form-item>
            <el-form-item label="确认新密码" prop="name">
              <el-input
                style="width:250px"
                v-model="userPass.name"
                placeholder="请再次输入新密码"
                clearable
              ></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer ac">
            <el-button size="small" class="small" type="primary" @click="submitForm('userPass')">确 认</el-button>
            <el-button size="small" class="small" @click="cancelForm">取消</el-button>
          </div>
        </div>
      </el-dialog>
      <div class="text fr">
        <div class="textCom">
          欢迎您, [
          <span>都市丽人实业有限公司</span> ] .
        </div>
        <div class="textBox">
          <span class="text_money">您的余额 1141462.51 元</span>
          <span class="text_card">储值卡剩余 307 张</span>
        </div>
      </div>
    </el-header>
    <!-- <el-header class="botHead">
      <el-menu class="el-menu-demo" mode="horizontal" :default-active="count">
        <template v-for="(item,index) in menuList">
          <el-menu-item
            @select="menuListOpen"
            v-if="!item.hidden"
            :index="item.children['0'].path"
            :key="index"
            router
          >{{item.name}}</el-menu-item>
        </template>
      </el-menu>
    </el-header>-->
    <el-container class="centerContainer">
      <el-aside width="auto">
        <div class="arrowBox" @click="isCollapse=!isCollapse;">
          <i :class="[isCollapse ?'el-icon-d-arrow-right': 'el-icon-d-arrow-left']"></i>
        </div>
        <el-menu
          default-active="1-4-1"
          class="el-menu-vertical-demo"
          :collapse="isCollapse"
          collapse-transition
          unique-opened
          router
        >
          <template v-for="item in $router.options.routes">
            <el-menu-item :index="item.path" :key="item.path" v-if="!item.children&&!item.hidden">
              <i :class="item.iconCls" class="icon iconfont"></i>
              <span slot="title">{{item.meta.title}}</span>
            </el-menu-item>
            <el-submenu :index="item.name" :key="item.name" v-if="!item.hidden&&item.children">
              <template slot="title">
                <i :class="item.iconCls" class="icon iconfont"></i>
                <span slot="title">{{item.meta.title}}</span>
              </template>
              <template v-for="child in item.children">
                <el-menu-item
                  :index="child.path"
                  :key="child.name"
                  v-if="!child.children"
                >{{child.meta.title}}</el-menu-item>
                <el-submenu :key="child.name" :index="child.name" v-if="child.children">
                  <span slot="title">{{child.meta.title}}</span>
                  <el-menu-item
                    v-for="grandson in child.children"
                    :key="grandson.name"
                    :index="grandson.path"
                  >{{grandson.meta.title}}</el-menu-item>
                </el-submenu>
              </template>
            </el-submenu>
          </template>
        </el-menu>
      </el-aside>
      <el-main>
        <el-breadcrumb separator="/">
          <el-breadcrumb-item v-for="(item,index) in breadlist" :key="index">{{item.meta.title}}</el-breadcrumb-item>
        </el-breadcrumb>
        <router-view :style="hei"></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
import { $getMenu } from "../api/index.js";
export default {
  name: "index",
  data() {
    return {
      hei: "",
      isCollapse: true,
      menuList: [],
      userPass: {},
      dialogFormVisible: false,

      // 验证
      rules: {
        name: [
          { required: true, message: "请输入旧密码", trigger: "blur" },
          { min: 8, message: "密码长度最少 8 位", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.hei = "min-height:" + (document.body.clientHeight - 134) + "px";
    this.getMenuList();
    this.getBread();
  },
  methods: {
    getMenuList() {
      $getMenu().then(res => {
        console.log(this.count);
        this.menuList = this.$router.options.routes;
      });
    },
    getBread() {
      this.breadlist = this.$route.matched;
      console.log(this.breadlist);
      this.$route.matched.forEach((item, index) => {
        //先判断父级路由是否是空字符串或者meta是否为首页，直接复写路径到根路径
        item.meta.title === "首页"
          ? (item.path = "/")
          : this.$route.path === item.path;
      });
    },
    // 用户中心
    handleCommand(e) {
      if (e == "changePassword") {
        this.dialogFormVisible = true;
      } else {
        this.$router.push({ path: "/login" });
        console.log(e);
      }
    },
    // 修改密码
    submitForm(userPass) {
      this.$refs[userPass].validate(valid => {
        console.log(this.formData);
        console.log(valid);
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
        this.dialogFormVisible = false;
      });
    },
    // 取消
    cancelForm() {
      this.dialogFormVisible = false;
    }
  },
  watch: {
    $route() {
      this.getBread();
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/varible.scss";
.topHead {
  width: 100%;
  // z-index: 999;
  // position: fixed;
  top: 0;
  left: 0;
  padding: 0px 20px;
  background-color: $slsyColor;
  .logoImg {
    margin: 10px;
    width: 230px;
    height: 40px;
    background-size: 100% 100%;
    background-image: url("data:image/png;base64,");
  }
  .text {
    position: relative;
    width: 450px;
    line-height: 60px;
    color: $fontColorFFF;
    .textCom {
      position: absolute;
      height: 60px;
    }
    .textBox {
      position: absolute;
      height: 60px;
      width: 200px;
      left: 265px;
      .text_money {
        font-size: 12px;
        line-height: auto;
        position: absolute;
        top: -10px;
        left: 0px;
      }
      .text_card {
        font-size: 12px;
        line-height: auto;
        position: absolute;
        top: 10px;
        left: 0px;
      }
      &::after {
        position: absolute;
        top: 15px;
        right: 40px;
        content: "";
        width: 1px;
        height: 30px;
        background: #999;
      }

      &::before {
        position: absolute;
        top: 15px;
        left: -26px;
        content: "";
        width: 1px;
        height: 30px;
        background: #999;
      }
    }
  }
  .handle {
    cursor: pointer;
    line-height: 60px;
    .icon {
      vertical-align: middle;
      font-size: 40px;
      margin-right: 5px;
    }
    .el-dropdown {
      font-size: $fontSize16;
      color: $fontColorFFF;
    }
  }
  .userPass {
  }
}
.botHead {
  //   border-bottom: 1px solid #eee;
  //   padding: 0px 30px;
  //   height: 46px !important;
  //   .el-menu--horizontal {
  //     border: none;
  //   }
  //   .el-menu--horizontal > .el-menu-item {
  //     color: $fontColor666;
  //     font-size: $fontSize16;
  //     font-weight: bold;
  //     height: 45px;
  //     line-height: 45px;
  //     padding: 0 30px;
  //     &.is-active {
  //       color: $hoverColor !important;
  //       border-bottom: 2px solid $hoverColor !important;
  //     }
  //   }
  //   .el-menu--horizontal > .el-menu-item:hover {
  //     color: $hoverColor !important;
  //   }
}
.centerContainer {
  // margin-top: 60px;
}
.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: left;
  height: 100%;
  .arrowBox {
    padding: 20px 0 0px;
    text-align: center;
    background: #fff;
    font-size: 18px;
    cursor: pointer;
    color: #666;
  }
  .el-menu-item.is-active {
    color: $hoverColor !important;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .icon {
    font-size: 22px;
    margin-right: 10px;
  }
}
.el-main {
  background-color: #e9eef3;
  color: #333;
  // height: 800px;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
  border: none;
}
.el-breadcrumb {
  padding-bottom: 20px;
}
.submenu {
  float: right;
}
.buttonimg {
  height: 60px;
  background-color: transparent;
  border: none;
}
.showimg {
  width: 26px;
  height: 26px;
  position: absolute;
  top: 17px;
  left: 17px;
}
.showimg:active {
  border: none;
}
</style>


