<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>礼品卡配置</div>
    </div>
    <div class="mainTable_swith">
      <el-row>
        <el-col :span="20">
          <div>
            温馨提示 : 开启后 , 售卡规则返现生效 , 配置售卡返现规则
            <span class="colBlue">( 如售卡100送10元 )</span> , 配置成功后 , 粉丝可通过该规则享受售卡返现优惠 .
          </div>
        </el-col>
        <el-col :span="4">
          <div class="fr txt">购卡活动
            <el-switch v-model="switchData.delivery"></el-switch>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="20">
          <div>温馨提示 : 开启后 , 用户可在小程序购卡功能中输入购卡金额 . 关闭后 , 用户只能按商户设定可选购卡金额购卡 .</div>
        </el-col>
        <el-col :span="4">
          <div class="fr txt">购卡自定义金额
            <el-switch v-model="switchData.delivery"></el-switch>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="20">
          <div>温馨提示 : 开启后 , 用户可在卡券中心 - 礼品卡功能进行申请礼品卡余额退款 , 已使用礼品卡不能退款 .</div>
        </el-col>
        <el-col :span="4">
          <div class="fr txt">礼品卡退款开关
            <el-switch v-model="switchData.delivery"></el-switch>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="mainTable_other">
      <el-row>
        <el-col :span="12">
          <div class="title">
            礼品卡有效期
            <span>
              ( 设置有效期之后 , 礼品卡余额到期
              <label for>自动清零</label> . )
            </span>
          </div>
          <div class="select">有效期 : &nbsp;
            <el-select v-model="formData.region" placeholder="请选择有效时间" clearable>
              <el-option label="永久有效" value="1"></el-option>
              <el-option label="有效期 30 天" value="2"></el-option>
              <el-option label="有效期 90 天" value="3"></el-option>
              <el-option label="有效期 180 年" value="4"></el-option>
              <el-option label="有效期 1 年" value="5"></el-option>
              <el-option label="有效期 2 年" value="6"></el-option>
              <el-option label="有效期 3 年" value="7"></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="title">
            可选购卡金额
            <span>
              ( 将会出现现在顾客的购卡页面中 , 最多
              <label for>6</label> 个 . )
            </span>
          </div>
          <div class="money">
            <ul>
              <li v-for="(item, index) in moneyList" :key="index">
                <i class="el-icon-circle-close-outline" @click="delMoney(index)"></i>
                {{item}}
              </li>
            </ul>
            <el-button class="btn" type="primary" icon="el-icon-plus" @click="addMoney"></el-button>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <div class="title">
            礼品卡卡面上传
            <span>( 用户前端礼品卡展示卡面内容 , 最多 4 张 , 无活动默认展示 , 图片建议尺寸 : 750*300 , 不得超过2M , 图片格式为 : '.jpg , .gif , .png' . )</span>
          </div>
          <div class="upDataImg">
            <el-upload
              :limit="4"
              action="https://luckypay.slsy.com/web/api/upload"
              list-type="picture-card"
              :on-success="sucImg"
              :on-remove="remImg"
            >
              <i class="el-icon-plus"></i>
              <div class="el-upload__text">添 加 图 片</div>
            </el-upload>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="mainTable_content">
      <el-row>
        <el-col :span="24">
          <div class="title">
            购卡活动规则
            <span>( 可添加不同购卡赠送规则 , 定时开关开启后到开启时间自动开启 , 结束时间自动关闭 , 一个时间段只可开启一个活动 .)</span>
          </div>
        </el-col>
      </el-row>
      <table-base
        :tableBut="tableBut"
        :tableSelection="tableSelection"
        :tableExpand="tableExpand"
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableOperate="tableOperate"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        @butEvent="butEvent"
        @operateEvent="operateEvent"
        @switchEvent="switchEvent"
        @sortEvent="sortEvent"
        @selectionEvent="selectionEvent"
        @filterEvent="filterEvent"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
    <div class="mainTable_dialog">
      <el-dialog
        width="1000px"
        title="购卡规则"
        top="10vh"
        v-if="dialogFormVisible"
        :visible.sync="dialogFormVisible"
        :close-on-click-modal="false"
      >
        <config-edit :formData="formData" @cancelForm="cancelForm" @click="submitForm"></config-edit>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import configEdit from "./configEdit.vue";
export default {
  name: "CardConfig",
  data() {
    return {
      moneyList: [100, 200, 500, 800],
      switchData: {},
      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      tableBut: [{ name: "新 增", value: "edit", show: true }],
      tableHead: [
        { name: "活动名称", value: "name" },
        { name: "活动类型", value: "id" },
        { name: "开始时间", value: "date2", sort: true },
        { name: "结束时间", value: "date", sort: true },
        { name: "定时开关", value: "switch", switch: true },
        {
          name: "状态",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "已开启" : "已关闭");
          },
          filter: [
            { text: "已开启", value: "1" },
            { text: "已关闭", value: "2" }
          ]
        }
      ],
      tableData: [
        {
          id: 1,
          date2: "2016-05-02",
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          switch: true
        },
        {
          id: 2,
          date2: "2016-05-02",
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          progress: 30,
          switch: true
        },
        {
          id: 3,
          date2: "2016-05-02",
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date2: "2016-05-02",
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          progress: 90,
          switch: true
        },
        {
          id: 5,
          date2: "2016-05-02",
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          progress: 100,
          switch: false
        }
      ],
      tableOperate: [
        {
          name: "开启",
          value: "open",
          show: function(e) {
            return e.status != 1 ? true : false;
          }
        },
        {
          name: "关闭",
          value: "close",
          show: function(e) {
            return e.status == 1 ? true : false;
          }
        },
        {
          name: "编辑",
          value: "edit",
          show: function(e) {
            return true;
          }
        },
        {
          name: "删除",
          value: "delete",
          show: function(e) {
            return true;
          }
        }
      ]
    };
  },
  // 注册组件
  components: {
    configEdit
  },
  methods: {
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    sortEvent(e) {
      console.log(e);
    },
    switchEvent(e) {
      console.log(e.name);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    import(e) {
      console.log("import");
    },
    export(e) {
      console.log("export");
    },
    open(e) {
      console.log(e.name);
    },
    close(e) {
      console.log(e.name);
    },
    edit(e) {
      this.formData = e || {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: ""
      };
      this.dialogFormVisible = true;
    },
    delete(e) {
      if (e) {
        console.log(e.name);
        this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else {
        console.log("批量删除");
      }
    },
    check(e) {
      console.log(e.name);
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },

    addMoney() {
      this.$prompt("请输入用户可充值的金额 !", "添加可选金额", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: "格式不正确",
        inputPlaceholder: "请输入金额"
      })
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: "你的手机是: " + value
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消输入"
          });
        });
    },
    delMoney(e) {
      this.moneyList.splice(e, 1);
    },
    sucImg(e) {
      console.log(e);
    },
    remImg(e) {
      console.log(e);
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../../assets/css/varible.scss";

.el-row {
  line-height: 40px;
  .colBlue {
    color: $hoverColor;
  }
  .title {
    font-weight: bold;
    padding-left: 15px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 3px;
      height: 16px;
      top: 12px;
      left: 3px;
      background: $hoverColor;
    }
    span {
      font-size: 14px;
      color: $fontColor666;
      label {
        color: $fontColorRed;
      }
    }
  }
}
.mainTable_swith {
  .txt {
    font-size: 15px;
    font-weight: bold;
    .el-switch {
      margin-left: 20px;
    }
  }
}
.mainTable_other {
  margin-top: 10px;
  .title {
    font-weight: bold;
    padding-left: 15px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 3px;
      height: 16px;
      top: 12px;
      left: 3px;
      background: $hoverColor;
    }
    span {
      font-size: 14px;
      color: $fontColor666;
      label {
        color: $fontColorRed;
      }
    }
  }
  .select {
    padding-left: 20px;
    margin: 10px 0;
  }
  .money {
    padding-left: 20px;
    margin: 10px 0;
    ul {
      li {
        text-align: center;
        border: 1px solid #dcdfe6;
        border-radius: 4px;
        float: left;
        width: 80px;
        height: 40px;
        position: relative;
        margin-right: 20px;
        line-height: 40px;
        position: relative;
        i {
          color: $hoverColor;
          position: absolute;
          top: 2px;
          right: 2px;
          cursor: pointer;
          display: none;
        }
        &:hover {
          border: 1px solid $hoverColor;
          box-shadow: 0 2px 12px 0 rgba(57, 130, 209, 0.2) !important;
          i {
            display: inline;
          }
        }
      }
    }
    .btn {
      font-size: 20px;
      padding: 9px 20px;
    }
  }
  .upDataImg {
    padding-left: 20px;
    margin: 10px 0;
  }
}
</style>




