<template>
  <div class="dialogBox sizeInline">
    <el-form
      inline
      :model="formData"
      :rules="rules"
      size="medium"
      ref="formData"
      label-width="85px"
      label-position="right"
    >
      <el-form-item label="活动名称" prop="name">
        <el-input v-model="formData.name" placeholder="请选输入活动名称" clearable></el-input>
      </el-form-item>
      <el-form-item label="活动时间" prop="time">
        <el-date-picker
          clearable
          v-model="formData.time"
          type="datetimerange"
          align="right"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
        ></el-date-picker>
      </el-form-item>
      <!-- <el-form-item label="活动类型" prop="type">
        <el-radio-group v-model="formData.type" @change="typeRadio">
          <el-radio label="money">购卡返现</el-radio>
          <el-radio label="card">购卡返券</el-radio>
        </el-radio-group>
      </el-form-item>-->
      <el-form-item label="购卡规则">
        <el-radio-group v-model="formData.buy" @change="buyRadio">
          <el-radio style="line-height: 36px;" label="firstbuy">每次购卡享受规则 (每次购卡都享受赠送规则)</el-radio>
          <el-radio style="line-height: 36px;" label="nofirstbuy">首次购卡享受规则 (首次购卡享受赠送规则, 之后不再享受赠送规则)</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="赠送规则">
        <el-radio-group v-model="formData.give" @change="giveRadio">
          <el-radio style="line-height: 36px;" label="unite">统一赠送 (例如: 购卡每满100元送10元)</el-radio>
          <el-radio style="line-height: 36px;" label="ladder">阶梯赠送 (例如: 购卡满100元送5元, 购卡满200元送15元)</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="购卡赠送" prop="name">
        <ul>
          <li class="ruleBox" v-for="(item, index) in ruleArr" :key="index">
            <el-input class="wid160 ac" v-model="item.name1" placeholder="请选输入购卡金额" clearable></el-input>&nbsp; 元 &nbsp; , &nbsp; 送 &nbsp;
            <el-input class="wid160 ac" v-model="item.name2" placeholder="请选输入赠送金额" clearable></el-input>&nbsp; 元
            <i class="el-icon-circle-plus font30 vm ml10 cupo hide" @click="addRuls"></i>
            <i class="el-icon-remove font30 vm ml10 cupo" @click="delRuls(index)"></i>
          </li>
        </ul>
        <!-- <ul>
          <li class="ruleBox" v-for="(item, index) in ruleArr" :key="index">
            <el-input class="wid160 ac" v-model="item.name1" placeholder="请选输入购卡金额" clearable></el-input>&nbsp; 元 &nbsp; , &nbsp; 送 &nbsp;
            <span>0</span>&nbsp; 张卡券 &nbsp;
            <el-button
              type="primary"
              size="mini"
              style="padding:10px 15px"
              @click="dialogCardList = true"
            >选择卡券</el-button>
            <i class="el-icon-circle-plus font30 vm ml10 cupo hide" @click="addRuls"></i>
            <i class="el-icon-remove font30 vm ml10 cupo" @click="delRuls(index)"></i>
          </li>
        </ul>-->
      </el-form-item>
      <el-form-item label="活动简介" prop="desc">
        <el-input
          type="textarea"
          resize="none"
          style="width:800px;"
          :autosize="{ minRows: 4, maxRows: 6}"
          v-model="formData.desc"
        ></el-input>
      </el-form-item>
      <el-form-item label="上传大图" prop="resource">
        <el-upload
          class="avatar-uploader textRightImgB"
          action="https://luckypay.slsy.com/web/api/upload"
          :show-file-list="false"
          :on-success="sucImgB"
          :before-upload="beforeImgB"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          <div class="el-upload__text" v-if="!imageUrl">添 加 图 片</div>
          <div
            class="el-upload__tip"
            slot="tip"
          >图片建议尺寸 : 400*150, 不得超过2M, 图片格式为 : '.jpg, .gif, .png'.</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="上传小图" prop="resource">
        <div class="item_form_wid1000">
          <el-upload
            :limit="4"
            action="https://luckypay.slsy.com/web/api/upload"
            list-type="picture-card"
            :on-success="sucImgS"
            :before-upload="beforeImgS"
            :on-remove="remImgS"
          >
            <i class="el-icon-plus"></i>
            <div class="el-upload__text">添 加 图 片</div>
            <div
              class="el-upload__tip"
              slot="tip"
            >最多可添加4张, ( 图片建议尺寸 : 180*100px, 不得超过2M, 图片格式为 : '.jpg, .gif, .png'.)</div>
          </el-upload>
        </div>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer ac">
      <el-button type="primary" @click="submitForm('formData')">确 认</el-button>
      <el-button @click="cancelForm">取 消</el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "editUser",
  data() {
    return {
      ruleArr: [{ name1: "", name2: "" }],
      // 验证
      rules: {
        name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        region: [
          { required: true, message: "请选择活动区域", trigger: "change" }
        ],
        store: [
          { required: true, message: "请选择品牌门店", trigger: "change" }
        ],
        time: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ]
      },
      // 大图路径
      imageUrl: "",
      // 小图
      dialogImageUrl: "",
      dialogCardList: false
    };
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    typeRadio(e) {
      console.log(e);
    },
    buyRadio(e) {
      console.log(e);
    },
    giveRadio(e) {
      console.log(e);
    },
    addRuls() {
      this.ruleArr.push({ name1: "", name2: "" });
    },
    delRuls(e) {
      if (this.ruleArr.length > 1) {
        this.ruleArr.splice(e, 1);
      }
    },
    // 初始化
    init() {},
    // 级联动
    cascaderChange(val) {
      console.log(val);
    },
    // 上传大图
    sucImgB(res, file) {
      console.log(res);
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeImgB(file) {
      console.log(file);
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 上传小图
    sucImgS() {},
    beforeImgS() {},
    remImgS(file, fileList) {
      console.log(file, fileList);
    },
    // 树形控件
    treeClick(e) {
      console.log(this.$refs.tree.getCheckedKeys());
    },

    // 确认
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.formData);
        console.log(valid);
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });

      this.$emit("submitForm");
    },
    // 取消
    cancelForm() {
      this.$emit("cancelForm");
    }
  }
};
</script>
<style lang="scss" scoped>
.ruleBox {
  margin-bottom: 10px;
  &:nth-last-of-type(1) {
    margin-bottom: 0;
    i {
      display: inline;
    }
  }
}
</style>




