<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>免单活动</div>
    </div>
    <div class="mainTable_swith">
      <el-row>
        <el-col :span="20">
          <div>温馨提示 : 活动总开关, 开启后, 免单活动即生效. 关闭后, 免单活动失效.</div>
        </el-col>
        <el-col :span="4">
          <div class="fr txt">免单活动
            <el-switch v-model="switchData.delivery"></el-switch>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="mainTable_txt">
      <div class="title">使用说明 ：</div>
      <div class="txt">
        <el-input
          type="textarea"
          resize="none"
          :autosize="{ minRows: 4, maxRows: 6}"
          v-model="formData.desc"
          placeholder="请在这里输入使用说明"
        ></el-input>
      </div>
      <div class="btn">
        <el-button type="primary" @click="submitForm('formData')">提 交</el-button>
      </div>
    </div>
    <div class="mainTable_content">
      <table-base
        :tableBut="tableBut"
        :tableSelection="tableSelection"
        :tableExpand="tableExpand"
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableOperate="tableOperate"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        @butEvent="butEvent"
        @operateEvent="operateEvent"
        @switchEvent="switchEvent"
        @sortEvent="sortEvent"
        @selectionEvent="selectionEvent"
        @filterEvent="filterEvent"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
    <div class="mainTable_dialog">
      <el-dialog
        width="580px"
        title="免单活动"
        top="10vh"
        v-if="dialogFormVisible"
        :visible.sync="dialogFormVisible"
        :close-on-click-modal="false"
      >
        <free-edit :formData="formData" @cancelForm="cancelForm" @click="submitForm"></free-edit>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import freeEdit from "./freeEdit.vue";
export default {
  name: "FreeSheet",
  data() {
    return {
      moneyList: [100, 200, 500, 800],
      switchData: {},
      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      tableBut: [{ name: "新 增", value: "edit", show: true }],
      tableHead: [
        { name: "活动名称", value: "name" },
        { name: "开始时间", value: "date2", sort: true },
        { name: "结束时间", value: "date", sort: true },
        {
          name: "门店属性",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "直营店" : "加盟/联营");
          },
          filter: [
            { text: "直营店", value: "1" },
            { text: "加盟/联营", value: "2" }
          ]
        },
        { name: "免单倍数", value: "name" },
        {
          name: "状态",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "已开启" : "已暂停");
          },
          filter: [
            { text: "已开启", value: "1" },
            { text: "已暂停", value: "2" },
            { text: "已过期", value: "3" }
          ]
        },
        { name: "创建时间", value: "date", sort: true }
      ],
      tableData: [
        {
          id: 1,
          date2: "2016-05-02",
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          switch: true
        },
        {
          id: 2,
          date2: "2016-05-02",
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          progress: 30,
          switch: true
        },
        {
          id: 3,
          date2: "2016-05-02",
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date2: "2016-05-02",
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          progress: 90,
          switch: true
        },
        {
          id: 5,
          date2: "2016-05-02",
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          progress: 100,
          switch: false
        }
      ],
      tableOperate: [
        {
          name: "开启",
          value: "open",
          show: function(e) {
            return e.status != 1 ? true : false;
          }
        },
        {
          name: "关闭",
          value: "close",
          show: function(e) {
            return e.status == 1 ? true : false;
          }
        },
        {
          name: "编辑",
          value: "edit",
          show: function(e) {
            return true;
          }
        },
        {
          name: "删除",
          value: "delete",
          show: function(e) {
            return true;
          }
        }
      ]
    };
  },
  // 注册组件
  components: {
    freeEdit
  },
  methods: {
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    sortEvent(e) {
      console.log(e);
    },
    switchEvent(e) {
      console.log(e.name);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    import(e) {
      console.log("import");
    },
    export(e) {
      console.log("export");
    },
    open(e) {
      console.log(e.name);
    },
    close(e) {
      console.log(e.name);
    },
    edit(e) {
      this.formData = e || {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: ""
      };
      this.dialogFormVisible = true;
    },
    delete(e) {
      if (e) {
        console.log(e.name);
      } else {
        console.log("批量删除");
      }
    },
    check(e) {
      console.log(e.name);
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.el-row {
  line-height: 40px;
}
.mainTable_swith {
  .txt {
    font-size: 15px;
    font-weight: bold;
    .el-switch {
      margin-left: 20px;
    }
  }
}
.mainTable_txt {
  margin-top: 10px;
  .title {
    font-weight: bold;
    font-size: 15px;
  }
  .txt {
    margin: 10px 0;
  }
  .btn {
    text-align: right;
    .el-button {
      padding: 8px 12px;
    }
  }
}
</style>


