<template>
  <div class="dialogBoxTab">
    <el-tabs type="border-card">
      <el-tab-pane label="基本资料">
        <el-row v-for="(item,index) in userInfo" :key="index">
          <el-col :span="2" class="ac">
            <div>{{item.name}}</div>
          </el-col>
          <el-col :span="1" class="ac">
            <div>:</div>
          </el-col>
          <el-col :span="6" class="al">
            <div>
              {{dataList[item.value]}}
              <i
                class="el-icon-edit phoneEdit"
                v-if="item.modify"
                @click="openPhone(dataList[item.value])"
              ></i>
            </div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="用户绑定品牌">
        <div class="titleText">
          用户现属门店 :
          <span>都市丽人廊坊店</span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          用户现属店员 :
          <span>张华生</span>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
export default {
  name: "userEdit",
  data() {
    return {
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      userInfo: [
        { name: "姓名", value: "name" },
        { name: "性别", value: "sex" },
        { name: "生日", value: "birth" },
        { name: "职务", value: "duties" },
        { name: "邮箱", value: "mail" },
        { name: "加入时间", value: "data" },
        { name: "手机号码", value: "phone", modify: true }
      ],

      dataList: {
        name: "杨淑华",
        sex: "男",
        birth: "1991.10.8",
        duties: "程序员",
        mail: "243326928@qq.com",
        data: "2018.10.08",
        phone: "18929300245"
      },

      tableHead: [
        { name: "所属品牌", value: "name" },
        { name: "所属区域", value: "date" },
        { name: "所属门店", value: "address" },
        { name: "所属店员", value: "progress" },
        { name: "会员卡号", value: "id" },
        { name: "积分余额", value: "status" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          address: "哈哈 1518 弄",
          progress: 10
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          address: "哈哈 1517 弄",
          progress: 30
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          address: "哈哈 1519 弄",
          progress: 60
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          address: "哈哈 1516 弄",
          progress: 90
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          address: "哈哈 1516 弄",
          progress: 100
        }
      ]
    };
  },
  // 注册组件
  components: {
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
    console.log(this.$root.fathData)
  },
  methods: {
    // 初始化
    init() {
      console.log(this.formData);
    },
    // 修改手机号码
    openPhone() {
      this.$prompt(
        // "建议在修改前联系粉丝, 确认粉丝身份信息后再修改.修改成功后我们会微信通知粉丝 !",
        "请输入新手机号",
        "修改手机号码",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: "格式不正确",
          inputPlaceholder: "请输入新手机号"
        }
      )
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: "你的手机是: " + value
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消输入"
          });
        });
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    }
  }
};
</script>
<style lang="scss" scoped>
.el-row {
  font-size: 16px;
  line-height: 40px;
  .phoneEdit {
    margin-left: 10px;
    cursor: pointer;
    font-size: 18px;
  }
}
.titleText {
  font-size: 18px;
  font-weight: bold;
}
</style>




