<template>
  <div class="login-wrap">
    <el-row>
      <el-col :span="15">
        <div class="login-img"></div>
      </el-col>
      <el-col :span="9">
        <div class="login-logo"></div>
        <el-form
          label-position="left"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="0px"
          class="demo-ruleForm login-container"
        >
          <h3 class="title">用 户 登 录</h3>
          <el-form-item prop="username">
            <el-input type="text" v-model="ruleForm.username" auto-complete="off" placeholder="账号"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              type="password"
              v-model="ruleForm.password"
              auto-complete="off"
              placeholder="密码"
            ></el-input>
          </el-form-item>
          <el-row>
            <el-col :span="16">
              <el-form-item prop="code">
                <el-input
                  type="text"
                  v-model="ruleForm.code"
                  auto-complete="off"
                  placeholder="图形验证码"
                  @keyup.enter.native="submitForm('ruleForm')"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8" class="code-box">
              <img :src="codeimg" alt class="codeimg" @click="getcode()" />
            </el-col>
          </el-row>
          <!-- <el-checkbox class="remember" v-model="rememberpwd">记住密码</el-checkbox> -->
          <el-form-item style="width:100%;">
            <el-button class="btnEnter" type="primary" @click="submitForm('ruleForm')">登 录</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <div class="login-text">© 深圳市商连商用电子技术有限公司提供技术支持</div>
  </div>
</template>
<script>
import { $login } from "../api/user.js";
import { setCookie, getCookie, delCookie } from "../utils/util";
export default {
  name: "login",
  data() {
    return {
      codeimg: "", // 验证码路径
      //定义loading默认为false
      logining: false,
      // 记住密码
      rememberpwd: false,
      ruleForm: {
        //username和password默认为空
        username: "",
        password: "",
        code: ""
        // randomStr: "",
        // codeimg: ""
      },
      //rules前端验证
      rules: {
        username: [{ required: true, message: "请输入账号", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        code: [{ required: true, message: "请输入验证码", trigger: "blur" }]
      }
    };
  },
  // 创建完毕状态(里面是操作)
  created() {
    // this.$message({
    //   message: "账号密码及验证码不为空即可",
    //   type: "success"
    // });
    // 获取图形验证码
    this.getcode();
    // 获取存在本地的用户名密码
    // this.getuserpwd();
  },
  // 里面的函数只有调用才会执行
  methods: {
    // 获取用户名密码
    // getuserpwd() {
    //   if (getCookie("user") != "" && getCookie("pwd") != "") {
    //     this.ruleForm.username = getCookie("user");
    //     this.ruleForm.password = getCookie("pwd");
    //     this.rememberpwd = true;
    //   }
    // },
    //获取info列表
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.logining = true;
          // 测试通道，不为空直接登录
          // setTimeout(() => {
          //   this.logining = false;
          //   this.$store.commit("login", "true");
          if (
            this.ruleForm.username == "admin" &&
            this.ruleForm.password == 123456 &&
            this.ruleForm.code == 1234
          ) {
            this.$router.push({ path: "/user/UserInfo" });
          } else {
            this.$message.error("输入错误");
          }
          // }, 1000);
          // 注释
          // $login(this.ruleForm).then(res => {
          //   console.log(res);
          // });
        } else {
          // 获取图形验证码
          this.getcode();
          // this.$message.error("请输入用户名密码！");
          this.logining = false;
          return false;
        }
      });
    },
    // 获取图形验证码
    getcode() {
      this.codeimg =
        "https://luckypay.slsy.com/web/api/loginCode?vt=" +
        new Date().getTime();
    }
  }
};
</script>

<style lang="scss" scoped>
.login-wrap {
  position: absolute;
  left: 0;
  bottom: 0;
  top: 0;
  right: 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding-top: 7%;
  background-repeat: no-repeat;
  background-size: 100%;
}
.login-text {
  width: 100%;
  position: absolute;
  bottom: 30px;
  opacity: 0.8;
  text-align: center;
  color: #fff;
  font-size: 16px;
}
.login-img {
  margin: 0 auto;
  width: 720px;
  height: 580px;
  background-repeat: no-repeat;
  background-size: 100%;
}
.login-logo {
  // margin: 0 auto;
  margin-left: 40px;
  color: #fff;
  height: 67px;
  width: 320px;
  background-repeat: no-repeat;
  background-size: 100%;
  margin-bottom: 10px;
  font-size: 30px;
}
.login-container {
  border-radius: 10px;
  width: 320px;
  padding: 30px 35px 15px 35px;
  border: 1px solid #eaeaea;
  text-align: left;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.title {
  font-weight: bold;
  font-size: 28px;
  margin: 0px auto 20px auto;
  text-align: center;
  color: #28a3df;
}
.remember {
  margin: 0px 0px 35px 0px;
}
.code-box {
  text-align: right;
}
.codeimg {
  height: 40px;
}
.btnEnter {
  width: 100%;
  font-size: 20px;
  background-color: #2898ec;
  color: #fff;
}

.login-wrap {
  background-image: url(data:image/png;base64,);
  .login-img {
    background-image: url(data:image/png;base64,);
  }
}
.login-logo {
  background-image: url(data:image/png;base64,);
}
</style>


