<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>分佣业绩月结</div>
    </div>

    <el-tabs tab-position="left" @tab-click="tabClick" value="store" stretch>
      <el-tab-pane name="store" label="门店月结">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
      <el-tab-pane name="clerk" label="店员月结">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
    </el-tabs>
    <div class="mainTable_dialog">
      <el-dialog title="门店分佣结算" :visible.sync="setDateBol" v-if="setDateBol" width="420px">
        <div class="dialogBoxS">
          <el-form
            :model="setDate"
            :rules="rules"
            size="medium"
            ref="setDate"
            label-width="100px"
            label-position="right"
          >
            <el-form-item label="选择月份" prop="name">
              <el-date-picker
                v-model="selectData"
                size="medium"
                placeholder="请选择需要结算的月份"
                type="month"
                clearable
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="选择品牌" prop="name">
              <el-select
                multiple
                filterable
                allow-create
                default-first-option
                size="medium"
                v-model="selectBrand"
                placeholder="请选择需要结算的品牌"
                clearable
              >
                <el-option
                  v-for="opt in optionsBrand"
                  :key="opt.value"
                  :label="opt.name"
                  :value="opt.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer ac">
            <el-button size="small" class="small" type="primary" @click="submitForm('setDate')">确 认</el-button>
            <el-button size="small" class="small" @click="cancelForm">取消</el-button>
          </div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
// import settleCheck from "./settleCheck.vue";
export default {
  name: "MonthlySettle",
  data() {
    return {
      type: "",
      tempName: "",
      optionsBrand: [
        {
          value: "HTML",
          label: "HTML"
        },
        {
          value: "CSS",
          label: "CSS"
        },
        {
          value: "JavaScript",
          label: "JavaScript"
        }
      ],
      searchData: {},
      balanceDate: "",
      selectBrand: [],
      formData: {},
      dialogFormVisible: false,
      setDateBol: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      searchList: [
        {
          id: "cascader",
          type: "cascader",
          name: "cascader",
          title: "品牌门店",
          placeholder: "请选择品牌门店"
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "结算时间",
          bplaceholder: "开始时间",
          eplaceholder: "结束时间",
          timeType: "daterange"
        }
      ],
      tableBut: [
        { name: "结算管理", value: "setDate", show: true },
        { name: "导 出", value: "export", show: true }
      ],
      tableHead: [
        { name: "品牌", value: "progress" },
        { name: "门店名称", value: "name" },
        { name: "储值总额 (元)", value: "date" },
        { name: "佣金结算", value: "date" },
        { name: "结算时间", value: "date" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          address: "哈哈 1518 弄",
          progress: 10,
          switch: true,
          img:
            "https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1546510062&di=e4f7d8cc8a9454427b3a469d7d78a6f2&src=http://img17.3lian.com/201612/20/37494c35be42137c1fb6b5b8e6912f44.png"
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          address: "哈哈 1517 弄",
          progress: 30,
          switch: true,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329861&di=1ddde06eed673a9d016fdf5c71ce2f68&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201609%2F18%2F20160918132951_vweJn.jpeg"
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          address: "哈哈 1519 弄",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          address: "哈哈 1516 弄",
          progress: 90,
          switch: true,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329858&di=e19621b4b431c3bea0a4ed1e91c69bdd&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201509%2F09%2F20150909173736_kvfYC.jpeg"
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          address: "哈哈 1516 弄",
          progress: 100,
          switch: false,
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546520329858&di=c04bf34a390ca5a9a015083034740eeb&imgtype=0&src=http%3A%2F%2Fpic31.photophoto.cn%2F20140405%2F0005018780796587_b.jpg"
        }
      ]
    };
  },
  // 注册组件
  components: {
    // settleCheck
  },
  methods: {
    tabClick(e) {
      this.type = e.name;
      if (e.name == "clerk") {
        this.searchList = [
          {
            id: "input",
            type: "input",
            name: "input",
            title: "店员名称",
            placeholder: "请选择店员名称"
          },
          {
            id: "input",
            type: "input",
            name: "input",
            title: "店员工号",
            placeholder: "请选择店员工号"
          },
          {
            id: "cascader",
            type: "cascader",
            name: "cascader",
            title: "品牌门店",
            placeholder: "请选择品牌门店"
          },
          {
            id: "daterange",
            type: "timeDate",
            name: "daterange",
            title: "结算时间",
            bplaceholder: "开始时间",
            eplaceholder: "结束时间",
            timeType: "daterange"
          }
        ];
        this.tableHead = [
          { name: "店员名称", value: "progress" },
          { name: "店员工号", value: "name" },
          { name: "角色", value: "date" },
          { name: "所属品牌", value: "date" },
          { name: "所属门店", value: "date" },
          { name: "门店属性", value: "date" },
          { name: "储值总额 (元)", value: "date" },
          { name: "结算佣金", value: "date" },
          { name: "结算时间", value: "date" }
        ];
      } else {
        this.searchList = [
          {
            id: "cascader",
            type: "cascader",
            name: "cascader",
            title: "品牌门店",
            placeholder: "请选择品牌门店"
          },
          {
            id: "daterange",
            type: "timeDate",
            name: "daterange",
            title: "结算时间",
            bplaceholder: "开始时间",
            eplaceholder: "结束时间",
            timeType: "daterange"
          }
        ];
        this.tableHead = [
          { name: "品牌", value: "progress" },
          { name: "门店名称", value: "name" },
          { name: "储值总额 (元)", value: "date" },
          { name: "佣金结算", value: "date" },
          { name: "结算时间", value: "date" }
        ];
      }
    },
    // 设置结算日期
    setDate(e) {
      console.log(111);
      this.setDateBol = true;
    },

    searchEvent(e) {
      console.log(e);
    },
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    export(e) {
      console.log("export");
      let tempName = this.type == "clerk" ? "店员月结" : "门店月结";
      this.$exportData(this.tableHead, this.tableData, tempName);
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },

    cancelForm() {
      console.log("取消");
      this.setDateBol = false;
    },
    submitForm() {
      console.log("确认");
      this.setDateBol = false;
    }
  }
};
</script>
<style lang="scss" scoped>
</style>





