<template>
  <div>
    <div class="mainTable_button">
      <el-button
        class="mainTable_but butNormal fr"
        v-for="item in tableBut"
        :key="item.value"
        @click="butEvent(item.value)"
        type="primary"
        size="medium"
        v-show="item.show"
        :class="item.name=='新 增'||item.name=='删 除'||item.name=='结算管理'?'fl':''"
      >{{item.name}}</el-button>
      <div class="clearfix"></div>
    </div>
    <div class="mainTable_talbe">
      <div class="mainTable_Content">
        <el-table
          stripe
          highlight-current-row
          style="width: 100%"
          :data="tableData"
          @selection-change="selectionEvent"
          @sort-change="sortEvent"
        >
          <!-- 选中 -->
          <el-table-column
            width="40"
            v-if="tableSelection"
            align="center"
            fixed="left"
            type="selection"
          ></el-table-column>
          <!-- 详情 -->
          <el-table-column width="40" v-if="tableExpand" align="center" fixed="left" type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item :label="item.name" v-for="item in tableHead" :key="item.value">
                  <span>{{ props.row[item.value] }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <!-- 顺序 -->
          <el-table-column width="40" v-if="tableIndex" align="center" fixed="left" type="index"></el-table-column>
          <div v-for="item in tableHead" :key="item.value">
            <!-- 筛选过滤器 -->
            <el-table-column
              width="100"
              v-if="item.filter"
              align="center"
              :label="item.name"
              :prop="item.value"
              :fixed="item.fixed"
              :formatter="item.format"
              :filters="item.filter"
              :filter-method="filterEvent"
            ></el-table-column>
            <!-- 进度条 -->
            <el-table-column
              v-else-if="item.progress"
              align="center"
              :label="item.name"
              :prop="item.value"
              :fixed="item.fixed"
            >
              <template slot-scope="scope">
                <el-progress
                  :text-inside="true"
                  :stroke-width="18"
                  :percentage="scope.row[item.value]"
                ></el-progress>
              </template>
            </el-table-column>
            <!-- 图片 -->
            <el-table-column
              width="100"
              v-else-if="item.img"
              align="center"
              :label="item.name"
              :prop="item.value"
              :fixed="item.fixed"
            >
              <template slot-scope="scope">
                <a v-if="item.download" :href="scope.row[item.value]" download="w3logo">
                  <img :src="scope.row[item.value]" class="talbe_pic">
                </a>
                <img v-if="!item.download" :src="scope.row[item.value]" class="talbe_pic">
              </template>
            </el-table-column>
            <!-- 开关 -->
            <el-table-column
              width="100"
              v-else-if="item.switch"
              align="center"
              :label="item.name"
              :prop="item.value"
              :fixed="item.fixed"
            >
              <template slot-scope="scope">
                <el-switch @change="switchEvent(scope.row)" v-model="scope.row[item.value]"></el-switch>
              </template>
            </el-table-column>
            <!-- 排序 -->
            <el-table-column
              v-else-if="item.sort"
              align="center"
              :label="item.name"
              :prop="item.value"
              :fixed="item.fixed"
              sortable="custom"
            ></el-table-column>
            <!-- 普通 -->
            <el-table-column
              v-else
              align="center"
              :label="item.name"
              :prop="item.value"
              :fixed="item.fixed"
              :formatter="item.format"
            ></el-table-column>
          </div>

          <el-table-column align="center" label="操作" fixed="right" v-if="tableOperate">
            <template slot-scope="scope">
              <span
                class="operate"
                v-for="item in tableOperate"
                :key="item.value"
                @click="operateEvent(item.value,scope.row)"
                type="text"
                size="medium"
                v-show="item.show(scope.row)"
              >{{item.name}}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="mainTable_Pagination">
        <el-pagination
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="sizeChangeEvent"
          @current-change="currentChangeEvent"
          :page-sizes="[10, 50, 100]"
          :page-size="tableSize"
          :total="tableTotal"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "tablebase",
  data() {
    return {};
  },
  props: [
    "tableBut",
    "tableSelection",
    "tableExpand",
    "tableIndex",
    "tableData",
    "tableOperate",
    "tableHead",
    "tableTotal",
    "tableSize"
  ],
  methods: {
    // 表格顶部按钮
    butEvent(eventName, val) {
      this.$emit("butEvent", { eventName, val });
    },

    // 表格内筛选过滤
    filterEvent(value, row, column) {
      this.$emit("filterEvent", { value, row, column });
      const property = column["property"];
      return row[property] === value;
    },

    // 表格内选择项
    selectionEvent(val) {
      const arr = [];
      val.forEach(e => {
        arr.push(e.id);
      });
      this.$emit("selectionEvent", arr);
    },

    // 表格内排序
    sortEvent(val) {
      this.$emit("sortEvent", val);
    },

    // 表格内操作
    operateEvent(eventName, val) {
      this.$emit("operateEvent", { eventName, val });
    },

    // 表格内开关
    switchEvent(val) {
      this.$emit("switchEvent", val);
    },

    imgEvent(row, column, cell, event) {
      console.log(row);
      console.log(column.property);
    },

    // 分页事件
    sizeChangeEvent(val) {
      this.$emit("sizeChangeEvent", val);
    },
    currentChangeEvent(val) {
      this.$emit("currentChangeEvent", val);
    }
  }
};
</script>
<style lang="scss" scoped>
.mainTable_button {
  padding: 5px 0;
  .el-button {
    margin: 0 5px;
  }
}
.mainTable_talbe {
  .mainTable_Content {
    .talbe_pic {
      height: 75px;
      width: 75px;
    }
    .demo-table-expand {
      font-size: 0;
    }
    .demo-table-expand label {
      width: 90px;
      color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
      margin-right: 0;
      margin-bottom: 0;
      width: 50%;
    }
    .operate {
      color: #409eff;
      cursor: pointer;
      margin-right: 10px;
      &:last-child {
        margin-right: 0px;
      }
    }
  }
  .mainTable_Pagination {
    text-align: right;
    padding: 20px 0;

    // 分页
    .el-pagination__editor.el-input {
      width: 50px;
    }
  }
}
</style>



