<template>
  <div class="dialogBox">
    <div class="mainTable_content">
      <table-base
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        :tableOperate="tableOperate"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
  </div>
</template>
<script>
export default {
  name: "settleCheck",
  data() {
    return {
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,
      tableHead: [
        { name: "订单编号", value: "name" },
        { name: "礼品卡金额消费 (元)", value: "date" },
        { name: "赠送金额消费 (元)", value: "address" },
        { name: "消费时间", value: "progress" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          address: "哈哈 1518 弄",
          progress: 10
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          address: "哈哈 1517 弄",
          progress: 30
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          address: "哈哈 1519 弄",
          progress: 60
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          address: "哈哈 1516 弄",
          progress: 90
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          address: "哈哈 1516 弄",
          progress: 100
        }
      ]
    };
  },
  // 注册组件
  components: {
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    // 初始化
    init() {
      console.log(this.formData);
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    }
  }
};
</script>
<style lang="scss" scoped>
.titleText {
  font-size: 18px;
  font-weight: bold;
}
</style>




