<template>
  <div class="dialogBox">
    <el-form size="medium" label-width="100px" label-position="right">
      <el-button class="btn" type="primary" @click="downTempExcel">下载 Excel 模版</el-button>
      <el-upload
        class="upload-demo"
        action
        :limit="1"
        :show-file-list="false"
        :on-change="importTempExcel"
        accept=".xlsx"
      >
        <el-button class="btn" type="primary">{{fileName}}</el-button>
      </el-upload>
      <el-button class="btn" type="primary">查看上传失败文件</el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  name: "importbox",
  data() {
    return {
      fileName: "请以模版为文件填入后上传"
    };
  },
  methods: {
    // 导入
    importTempExcel(file) {
      this.fileName = file.name;
      this.$emit("importTempExcel", file);
    },
    downTempExcel() {
      this.$emit("downTempExcel", "downTempExcel");
    }
  }
};
</script>
<style lang="scss" scoped>
.btn {
  width: 320px;
  display: block;
  margin-bottom: 20px;
  margin-left: 0;
  &:last-child {
    margin-bottom: 0px;
  }
}
</style>



